<template>
	<div class="system">
		<header-bar></header-bar>
		<div class="app-main">
			<div class="navBar" :class="sliderHandle?'slideIn':'slideOut'">
				<div class="nodes">
					<ul class="cls" v-for="(node,index) in sideData">
						<tree :node="node" :index="index"></tree>
					</ul>
					<!--<button type="button" class="btn btn-info btn-section" @click="addRootGroup"><i class="fa fa-plus"></i></button>-->
				</div>
			</div>
			<el-row class="exhibition-info">
				<div class="main-header-wrap">
					<div class="main-header">
						<div class="main-header-left">
							<i></i>
							<span class="appPresent">{{$t("currentLocation")}}：</span>
							<el-breadcrumb separator=">" class="main-crumbs">
								<el-breadcrumb-item v-for='(item, index) in showTreePaths' :key="index">
									<span @click='TreeShow(item._id, index)'>{{item.name}}</span>
								</el-breadcrumb-item>
							</el-breadcrumb>
						</div>
						<!--<div class="main-header-right">
							<el-input class="searchKey" :placeholder="$t('search')" icon="search" v-model="input2">
							</el-input>
						</div>-->
					</div>
				</div>
				<div class="main-content-wrap">
					<div class="main-content">
						<router-view></router-view>
					</div>
				</div>
			</el-row>
		</div>
		<transition name="fade">
			<div class="section-shade" v-if="Shade" @click="sectionHandle"></div>
		</transition>
		<transition name="fade">
			<div class="section-window" v-if="isShow">
				<div class="add-section-top">
					<span>{{$t("treeBox.addDepartment")}}</span>
					<i class="fa fa-close" @click="sectionHandle"></i>
				</div>
				<div class="add-section-info">
					<div class="form-group">
						<label for="">{{$t("treeBox.descend")}}</label>
						<input type="text" class="form-control" id="" v-model="getTreeParentInfo.name" disabled>
					</div>
					<div class="form-group">
						<label for="">{{$t("treeBox.name")}}</label>
						<el-input type="text" v-model="personnelNode" auto-complete="off" :placeholder="$t('treeBox.childName')" @keyup.enter.native="saveHandle"></el-input>
					</div>
				</div>
				<div class="add-section-btn">
					<button type="button" class="btn btn-info" @click="saveHandle">{{$t("treeBox.save")}}</button>
				</div>
			</div>
		</transition>
		<transition name="fade">
			<div class="section-window" v-if="plusCtrl">
				<div class="add-section-top">
					<span>{{$t("treeBox.addDepartment")}}</span>
					<i class="fa fa-close" @click="sectionHandle"></i>
				</div>
				<div class="add-section-info">
					<div class="form-group">
						<label for="">{{$t("treeBox.name")}}</label>
						<el-input type="text" v-model="rootGroupName" auto-complete="off" :placeholder="$t('treeBox.rootName')" @keyup.enter.native="saveHandle"></el-input>
					</div>
				</div>
				<div class="add-section-btn">
					<button type="button" class="btn btn-info save-btn" @click="saveHandle">{{$t("treeBox.save")}}</button>
				</div>
			</div>
		</transition>
		<!-- <transition name="fade">
        <div class="navigation" v-if="navMap">
            <div class="navigation-top">
              <span @click="mettingNav(1)" role="button"><img src="../../assets/images/criterion.png"></span>
              <span @click="mettingNav(0)" role="button"><img src="../../assets/images/fast.png"></span>
            </div>
            <div class="navigation-buttom" @click="mettingNav(2)">
              <p role="button">
                <span>跳过，进入主页</span>
                <i></i>
              </p>
            </div>
          </div>
    </transition> -->
	</div>
</template>

<script>
	import DirList from './packageJS/dirList.js'
	import {
		mapActions,
		mapGetters
	} from 'vuex'
	import headerBar from '../system/header/header'
	import Tree from '../system/myTree/myTree'
	import './system.scss'
	export default {
		name: 'System',
		computed: {
			...mapGetters([
				'isShow',
				'sliderHandle',
				'Shade',
				'sideData',
				'getTreeParentInfo',
				'showTreePaths',
				'navMap'
			])
		},
		data() {
			return {
				input2: '',
				personnelNode: '',
				rootGroupName: '',
				plusCtrl: false
			}
		},
		methods: {
			// tree路径显示
			TreeShow(id, index) {
				if(this.showTreePaths.length - 1 === index) {
					return;
				}
				this.$router.push({
					path: `/${id}/`
				})
				let treeInfo = DirList.GetDirList(this.sideData, id)
				this.$store.dispatch('showPaths', treeInfo)
			},
			addRootGroup() {
				this.$store.dispatch('getInformation')
				this.plusCtrl = true
			},
			sectionHandle() {
				this.$store.dispatch('allclear')
				this.plusCtrl = false
			},
			saveHandle() {
				//  创建tree子节点并保存
				if(!this.plusCtrl) {
					if(!this.personnelNode) {
						return;
					}
					this.$store.dispatch('getInformation')
					this.$http.CreateGroups({
							name: this.personnelNode,
							parent: this.getTreeParentInfo._id
						})
						.then(data => {
							this.$http.GetGroups()
								.then((data) => {
									this.$store.dispatch('createGroup', data.data)
									this.personnelNode = ''
									this.$message({
										message: this.$t("messageTip.addSuccess"),
										duration: 2000,
										type: 'success'
									});
								})
						})
					return;
				} else {
					//  创建tree根节点并保存	
					if(!this.rootGroupName) {
						return;
					}
					this.$store.dispatch('getInformation', this.rootGroupName)
					this.$http.CreateGroups({
							name: this.rootGroupName
						})
						.then(data => {
							this.$http.GetGroups()
								.then((data) => {
									this.$store.dispatch('createGroup', data.data)
									this.rootGroupName = ''
									this.$message({
										message: this.$t("messageTip.addSuccess"),
										duration: 2000,
										type: 'success'
									});
									if(!this.$route.params.id) {
										this.$router.push({
											path: `/${data.data[0]._id}/`
										})
										this.$store.dispatch('showPaths', [data.data[0]])
									}									
								})
						})
						.catch(error => {
							console.log(error)
						})
				}
				this.plusCtrl = false
			}

		},
		components: {
			Tree,
			headerBar
		},
		watch: {
			"$route.params.id":function(val, oldval){
				this.$store.dispatch('setOfficeList', this.showTreePaths[this.showTreePaths.length - 1])
			}
			
		},
		created() {
			if(this.$http.GetCurrentUserInfo()) {
				this.$store.dispatch('jurisdiction', this.$http.GetCurrentUserInfo())
			}
			this.$http.GetGroups()
				.then(data => {
					//	无有数据
					if(!data.data.length) {
				        this.$notify({
				          title: this.$t("messageTip.notify.warn"),
				          message: this.$t("messageTip.notify.message"),
				          type: 'warning'
				        });
				        return;
					}
					//	有数据且刚进入系统默认第一个
					if(!this.$route.params.id) {
						this.$router.push({
							path: `${data.data[0]._id}/`
						})
					}
					this.$store.dispatch('createGroup', data.data)
					// 路由显示预处理并发送
					let routerTemp = this.$route.path.split('/')
					let allDataTemp = DirList.GetDirList(data.data, this.$route.params.id)
					this.$store.dispatch('setOfficeList', allDataTemp[allDataTemp.length - 1])
					if(routerTemp[routerTemp.length - 1]) {
						for(let key in this.$t('allRouter')[0]) {
							if(routerTemp[routerTemp.length - 1] === key) {
								allDataTemp.push({
									name: this.$t('allRouter')[0][key]
								})
								break;
							}
						}
					}
					this.$store.dispatch('showPaths', allDataTemp)
				})
				.catch((error) => {
					if(error.response && error.response.status === 401) {
						window.localStorage.removeItem('token')
						window.localStorage.removeItem('userInfo')
						this.$router.push({name: 'Login'})
					}
				})
		}		
	}
</script>


