<template>
  <div class="official">
    <div class="official-header">
      <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
      <div class="official-handle" v-if="statusNum === 4">
        <!-- <el-button>{{$t('official.table.save')}}</el-button> -->
      </div>
    </div>
    <div class="officialTable">
      <table class="officialTable-table">
        <tr>
          <td>{{$t('official.table.title')}}</td>
          <td style="word-break:break-all">
            {{officialInfo.title}}
          </td>
        </tr>
        <tr>
          <td>{{$t('official.table.date')}}</td>
          <td>{{officialInfo.date | filtersTime}} </td>
        </tr>
        <tr>
          <td>{{$t('official.table.site')}}</td>
          <td>{{officialInfo.location}} </td>
        </tr>
        <!-- <tr>
          <td>{{$t('official.table.content')}}</td>
          <td style="word-break:break-all" v-html="officialInfo.content"></td>
        </tr> -->
        <tr>
          <td>{{$t('official.table.speaker')}}</td>
          <td>{{officialInfo.host}} </td>
        </tr>
        <tr>
          <td>{{$t('official.table.receiver')}}</td>
          <td v-if="statusNum === 4">
            <el-tag :key="name" type="primary" v-for="(name, index) in recipients">
              {{name}}
            </el-tag>
            <!-- <span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="officialTable-table-add">{{$t('official.table.receiverTd.selectStaff')}}</span></span> -->
          </td>
          <td v-else>
              <span :key="name" v-for="(name, index) in recipients">
                  {{name}}；
              </span>
          </td>
        </tr>
      </table>
    </div>
     <div class="official-Topic" v-for="info in officialInfo.topics" v-if="officialInfo.topics.length">
      <div class="official-content">
         <p>{{$t('official.submitter')}}：{{info.publisher.name}}</p>     
        <p>{{$t('official.type')}}：{{info.type}}</p>
        <p>{{$t('official.department')}}：{{info.department}}</p>
        <p>{{$t('official.supervisor')}}：{{info.supervisor}}</p>
        <p>{{$t('official.subTime')}}：{{info.updatedAt | filtersTime}}</p>
      </div>
      <div class="official-message">
        <h3>{{info.title}}</h3>
        <h5>{{$t('official.content')}}：</h5>
        <p v-html="info.content"></p>

        <!-- 投票 -->
        <h4 v-if="info.votes.length">投票</h4>
        <div class="official-Vote" v-for="msg in info.votes" v-if="info.votes.length">
          <h4 v-if="info.attachments">{{msg.title}}</h4>
          <label v-for="option in msg.options"> 
              <input :type="msg.type === 1 ? 'radio' : 'checkbox'" :name="msg._id">
              {{option.title}}
          </label>
        </div>

        <!-- 附件 -->
        <h4 v-if="info.attachments.length">附件</h4>
        <div class="official-Attachments" v-for="file in info.attachments" v-if="info.attachments">
            <a :href=file.path download>{{file.originalname}}</a>
        </div>
      </div>
    </div> 
  </div>
</template>

<script>
  import personnelList from '../personnelList/personnelList.vue'
  import './official.scss'
  export default {
    name: 'official',
    data() {
      return {
        recipients: [],
        recipientsID: [],
        statusNum: '',
        officialInfo: {
          confirmor: Object,
          title: '',
          content: '',
          date: '',
          location: '',
          personnel: Array,
          host: '',
          piblisher: Object,
          topics: Array
        }
      }
    },
    mounted() {
      if (!this.$route.query.id) return;
      this.$http.GetMeeting(this.$route.query.id)
        .then(res => {
            this.statusNum = res.data.status
            this.officialInfo = res.data
            res.data.personnel.forEach((item) => {
              this.recipients.push(item.name)
              this.recipientsID.push(item._id)
            })
            for(let item of this.officialInfo.topics){
                if(item.publisher === null){
                    item.publisher = ''
                }
            }
        })
    }
  }

</script>



