<template>
  <div class="addOfficiaConference" @click="baseHandle">
    <div class="addOfficiaConference-header">
      <div class="addOfficiaConference-header-back" @click.stop="$router.back()" role='button'>
        <i class="fa fa-angle-left fa-2x"></i>
        <span>{{$t('pageCommon.back')}}</span>
      </div>
      <div>
        <button class="add-btn" @click.stop="topicHandle('false')">{{$t('addConf.addConference')}}</button>
        <button class="add-btn" @click.stop="topicHandle('true')">{{$t('addConf.saveConference')}}</button>
      </div>
    </div>
    <div class="allMsg" ref="allMsg">
      <!-- =============会议部分===========  -->
      <div class="addOfficiaConference-body">
        <table class="addOfficiaConference-table">
          <tr>
            <td>{{$t('addConf.confName')}}</td>
            <td>
              <input type="text" class="addOfficiaConference-table-input" v-model="officialMeeting.title" />
            </td>
          </tr>
          <!-- <tr class="second-line">
                    <td colspan="2"></td>
                </tr> -->
          <tr>
            <td>{{$t('addConf.date')}}</td>
            <td>
              <div class="block">
                <el-date-picker v-model="officialMeeting.date" type="datetime" :placeholder="$t('addConf.dataTd.selectDate')">
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td>{{$t('addConf.site')}}</td>
            <td>
              <input type="text" class="addOfficiaConference-table-input" v-model="officialMeeting.location" />
            </td>
          </tr>
          <tr>
            <td>{{$t('addConf.speaker')}}</td>
            <!-- <td><input type="text" class="addOfficiaConference-table-input" v-model="officialMeeting.host" /></td> -->
            <td>
              <select v-model="officialMeeting.host" class="addOfficiaConference-table-input">
                <option value=""></option>
                <option :value=items.name v-for="items in hosthostList">{{items.name}}</option>
              </select>
            </td>
          </tr>
          <!-- <tr>
            <td>{{$t('colTopic.content')}}</td>
            <td>
              <quill-editor v-model="officialMeeting.content" :options="editorOption"></quill-editor>
            </td>
          </tr> -->
          <tr>
            <td>{{$t('addConf.receiver')}}</td>
            <td>
              <el-tag :key="name" type="primary" v-for="(name, index) in recipients" :closable="true" :close-transition="false" @close="delUser(name, index)">
                {{name}}
              </el-tag>
              <span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')">
                <i class="fa fa-plus fa-lg"></i>
                <span class="addOfficiaConference-table-add">{{$t('addConf.receiverTd.add')}}</span>
              </span>
            </td>
          </tr>
          <!-- <tr>
                <td>{{$t('addConf.require')}}</td>
                <td>
                <el-checkbox>{{$t('addConf.requireTd.receive')}}</el-checkbox>
                <el-checkbox>{{$t('addConf.requireTd.reply')}}</el-checkbox>	   				
                </td>
            </tr>
            <tr>
                <td>{{$t('addConf.level')}}</td>
                <td>
                <el-checkbox>{{$t('addConf.levelTd.secrecy')}}</el-checkbox>	   				
                </td>
            </tr>		   		 -->
        </table>
      </div>
      <!-- =============议题部分=========== -->
      <div class="addTopicTable table-responsive">
        <!-- <div class="header-topic" v-if="topics.length">
                          <h5>{{$t('addTopic.tip')}}</h5>
                      </div> -->
        <div v-for="(topic, index) in topics" class="topicBox" ref="topicBox" @click="topicIdx = index">
          <div class="delete-Topic" role="button" @click="deleteTopic(topics, topic, index)">
            <img src="../../../assets/images/ic_close.png" alt="">
          </div>
          <table class="table">
            <tbody>
              <tr class="row">
                <th class="col-md-2">{{$t('addTopic.topic')}}</th>
                <td colspan="5" class="col-md-12">
                  <input type="text" v-model="topic.title" @change="UpDataTopic(topic, null, index)">
                </td>
              </tr>
              <tr class="row">
                <th class="col-md-2">{{$t('addTopic.type')}}</th>
                <td class="col-md-2">
                  <select v-model="topic.type" @change="UpDataTopic(topic, 2, index)">
                    <option value=""></option>
                    <option :value="$t('addTopic.typeTd.vote')">{{$t('addTopic.typeTd.vote')}}</option>
                    <option :value="$t('addTopic.typeTd.report')">{{$t('addTopic.typeTd.report')}}</option>
                    <option :value="$t('addTopic.typeTd.research')">{{$t('addTopic.typeTd.research')}}</option>
                  </select>
                </td>
                <th class="col-md-2">{{$t('addTopic.department')}}</th>
                <td class="col-md-2">
                  <!-- ======================科室================= -->
                  <select v-model="topic.department" @change="UpDataTopic(topic, 1, index)">
                    <option value=""></option>
                    <option v-for="info in officeInfo" :value="info.name">{{info.name}}</option>
                  </select>
                </td>
                <th class="col-md-2">{{$t('addTopic.principal')}}</th>
                <td class="col-md-2">
                  <!-- ======================责任人================= -->
                  <!-- <select v-model="topic.supervisor" @change="UpDataTopic(topic, null, index)">
                                <option value=""></option>
                                <option v-for="data in userMsg[index]" :value="data.name">{{data.name}}</option>
                            </select> -->
                  <!-- 临时 -->
                  <select v-model="topic.supervisor" @change="UpDataTopic(topic, null, index)">
                    <option value=""></option>
                    <option v-for="users in topic.officeUsers" :value="users.child.name">{{users.child.name}}</option>
                  </select>
                </td>
              </tr>
              <!-- <tr class="row">
                        <th class="col-md-2">{{$t('addTopic.receiver')}}</th>
                        <td colspan="5" class="col-md-8">
                            <el-checkbox-group v-model="addTopicChecked">
                                <el-tag :key="name" type="primary" v-for="(name, topic) in recipients" :closable="true" :close-transition="false" @close="delUser(name, topic)">
                                    {{name}}
                                </el-tag>
                                <span class="officia-addTopic" @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i>{{$t('addTopic.selectStaff')}}</span>
                            </el-checkbox-group>
                        </td>
                    </tr> -->
              <tr class="row">
                <th rowspan="2" class="col-md-2">{{$t('addTopic.content')}}</th>
                <td colspan="5" class="col-md-10" style="padding:0;">
                  <quill-editor ref="quillEditor" v-model="topic.content" :options="editorOption" @focus="quillContent = true"></quill-editor>
                </td>
              </tr>
              <tr>
              </tr>
              <tr class="row">
                <th class="col-md-2">
                  {{$t('addRecord.upload')}}
                  <p class="ps">{{$t('uploading.limit')}}</p>
                  <p class="ps">(pdf、xls、xlsx、doc、ppt、pptx、docx、azw3)</p>
                </th>
                <td colspan="5" class="col-md-8">
                  <el-tag type="primary" v-for="(file, idx) in topic.attachments" :key="file._id" :closable="true" :close-transition="false"
                    @close="delFile(topic.attachments, idx, index)">
                    {{file.originalname}}
                  </el-tag>
                  <i class="fa fa-paperclip fa-lg"></i>
                  <vue-file-upload url='/api/attachments/uploaderFile' ref="vueFileUploader" v-bind:filters="filters" v-on:onChange="onChange(index)"
                    v-on:onAdd="onAddItem" v-bind:events="cbEvents" v-bind:request-options='datas' multiple :label="$t('uploading.attachment')" :autoUpload='atuoBol'>
                  </vue-file-upload>
                </td>
              </tr>
            </tbody>
          </table>
          <!-- 分割线 -->
          <!-- <p class="parting-line" v-if="showVote"></p> -->
          <!-- =============投票部分=========== -->
          <div class="addTopicTable-footer" v-if="topic.type === $t('addTopic.typeTd.research')">
            <div v-for="(vote, index) in topic.votes" class="Vote-Table-List" ref="topicVote">
              <el-button class="fa fa-times fa-lg" @click="removeVote(topic, topic.votes, index)"></el-button>
              <table class="vote-table">
                <tr>
                  <th class="footer-table-td11">{{$t('addTopic.vote.title')}}</th>
                  <td colspan="2">
                    <input type="text" class="addTopicTable-table-input" v-model="vote.title" />
                  </td>
                </tr>
                <tr>
                  <th class="footer-table-td12">{{$t('addTopic.vote.options')}}</th>
                  <td colspan="2">
                    <el-radio-group v-model="vote.type">
                      <el-radio :label="1">{{$t('addTopic.vote.optionsTd.radio')}}</el-radio>
                      <el-radio :label="2">{{$t('addTopic.vote.optionsTd.multiple')}}</el-radio>
                    </el-radio-group>
                  </td>
                </tr>
                <tr>
                  <th class="footer-table-td13" :rowspan="vote.options.length+1">{{$t('addTopic.vote.choice')}}</th>
                  <td class="footer-table-td25" @click="addOption(vote)">
                    <span role='button'>
                      <i class="fa fa-plus fa-lg"></i>
                      <span class="addTopicTable-table-add">{{$t('addTopic.vote.choiceTd.add')}}</span>
                    </span>
                  </td>
                </tr>
                <tr v-for="(option, index) in vote.options" ref="voteOptions">
                  <td>
                    <input type="text" class="options" v-model="option.title" @blur="UpDataTopic(topic, null, index)">
                    <i class="fa fa-times fa-lg" role='button' @click="removeVote(topic, vote.options, index)"></i>
                  </td>
                </tr>
              </table>
            </div>
            <div class="add-Vote">
              <span role='button' @click="addVote(topic, index)">
                <i class="fa fa-plus fa-lg"></i>
                <span class="addTopicTable-table-add">{{$t('addTopic.vote.addVote')}}</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- =============上传附件弹窗=========== -->
    <table class="file-upload-table" v-if="bol">
      <thead>
        <tr class="btn-info">
          <th class="file-name">{{$t('uploading.name')}}</th>
          <th>{{$t('uploading.size')}}</th>
          <th class="progress-show">{{$t('uploading.progress')}}</th>
          <th>{{$t('uploading.status')}}
            <i class="fa fa-window-close fa-2x" @click="fileCtrl()"></i>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(file,index) in tempFiles">
          <td>{{file.name}}</td>
          <td>{{(file.size/1024).toFixed(2)}}kb</td>
          <td>
            <div class="progress">
              <div class="progress-bar" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" :style='btn(file)'>
                {{file.progress}}%
              </div>
            </div>
          </td>
          <td>{{onStatus(file)}}</td>
        </tr>
        <tr>
          <td colspan="4" class="upload-all-wrap">
            <button @click='uploadAll' class='upload-all'>{{allUploadCtrl}}</button>
          </td>
        </tr>
      </tbody>
    </table>
    <personnel @userInfo="userInfo" ref="userData"></personnel>
  </div>
</template>

<script>
  import VueFileUpload from 'vue-file-upload';
  import {
    mapActions,
    mapGetters
  } from 'vuex'
  import personnel from '../personnelList/personnelList'
  import './addOfficiaConference.scss'
  export default {
    name: 'addOfficiaConference',
    components: {
      personnel,
      VueFileUpload
    },    
    computed: {
      ...mapGetters([
        'sideData',
        'officeList',
        'toolsInfoBack'
      ])
    },    
    data() {
      return {
        IDtips: 0,
        hosthostList: [],
        officeInfo: [],
        officeUsersArr: [],
        recipients: [],
        userMsg: [],
        topicEdit: true,
        atuoBol: false,
        bol: false,
        listenInfo: true,
        filtersFile: false,
        quillContent:false,
        tempFiles: [],
        id: '',
        topicAttachments: [],
        topics: [],
        allUploadCtrl: '',
        index: '',
        topicIndex: 0,
        topicIdx: '',
        addTopicChecked: '',
        editorOption: {
          placeholder: '',
          modules: {
            toolbar: [
	            [
              	'bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block',
              	{'header': 1}, {'header': 2}, {'list': 'ordered'}, {'list': 'bullet'},
								{'script': 'sub'}, {'script': 'super'}, {'direction': 'rtl'}, {'align': 'center'},
								'clean', 'link', 'image'
	            ]
            ],
            imageResize: {
              displayStyles: {
                backgroundColor: '#000',
                border: 'none',
                color: 'white'
              }
            }
          }
        },
        officialMeeting: {
          publisher: this.$http.GetCurrentUserInfo()._id,
          groups: this.$route.params.id,
          title: '',
          content: '',
          date: '',
          location: '',
          host: '',
          personnel: [],
          status: 4,
          type: '2',
          state: 'false'
        },
        // 过滤文件格式
        filters: [{
          name: "FilterFile",
          fn: function (file) {
            var type = '|' + file.name.slice(file.name.lastIndexOf('.') + 1) + '|';
            return '|pdf|xls|xlsx|doc|ppt|pptx|docx|azw3|'.indexOf(type) !== -1;
          }
        }],
        cbEvents: {
          // 获取上传附件返回的数据
          onCompleteUpload: (file, response, status, header) => {
            this.topics[this.topicIndex].attachments = this.topics[this.topicIndex].attachments.concat(response);
            if (!this.topics[this.topicIndex]._id) return;
            this.UpDataTopic(this.topics[this.topicIndex])
          },
          onAddFileFail: (file) => {
            this.filtersFile = true;
          }
        },
        datas: {
          formData: {
            // libraryId: '', 
            // uploadBy: '',
            // category: ''      		
          },
          headers: {
            'authorization': 'Bearer ' + window.localStorage.getItem('token')
          },
          withCredentials: false
        }
      }
    },
    methods: {
      baseHandle(){
        if(!this.quillContent) return;
        this.UpDataTopic(this.topics[this.topicIdx], null, this.topicIdx)
      },

      // ================================= 会议处理 ===============================
      addMeeting(n){
        // 创建会议
        if (!this.id) {
            this.$http.AddMeeting(this.officialMeeting)
              .then(res => {
                this.id = res.data._id
                this.listenInfo = false
                this.$http.MergePdf(this.id)
                n === 'true' ? this.$router.push({
                  name: 'Conference'
                }) : '';
              })
        } else {
          this.$http.MergePdf(this.id)
          n === 'true' ? this.$router.push({
            name: 'Conference'
          }) : '';
        }
      },
      
      // 更新会议
      upDateMeeting(){
        this.$http.UpDataMeeting(this.id, this.officialMeeting)
            .then(res => {
              this.$http.MergePdf(this.id)
              this.listenInfo = false
            })
      },
      
      // ==================================议题处理======================================
      topicHandle(n) {
        this.index = ''
        if (!this.officialMeeting.title || !this.officialMeeting.location || !this.officialMeeting.host || !this.officialMeeting
          .personnel.length) {
          this.$message({
            type: 'error',
            message: this.$t('addConf.msgTip.one')
          })
          return
        }
          if(this.topics.length){
            let eleArr = document.querySelectorAll('.ql-container>.ql-editor img')
            for(let ele of eleArr){
              if(ele.width > 540){
                this.$notify({title: this.$t('messageTip.images.warn'), message: this.$t('messageTip.images.message'), type: 'warning'})
                return;
              }
            }  

        }
        // 会议处理
        if (this.listenInfo && this.id) {
          this.upDateMeeting(n)
        } else {
          this.addMeeting(n)
        }
        
        this.addTopic(n)

      },
      
      // 添加议题
      addTopic(n){
        let arr = []

        this.topics.forEach((item, index) => {
          if (!item._id && item.title !== '') {          	
             this.topicEdit = true
            this.officeUsersArr.push(item.officeUsers)
            // 创建议题及投票
            for (let i = 0; i < item.votes.length; i++) {
              for (let j = 0; j < item.votes[i].options.length; j++) {
                item.votes[i].options[j].index = j
              }
            }
            item.publisher = this.$http.GetCurrentUserInfo()._id
            arr.push(
              this.$http.CreateTopic(this.id, item).then(res => {
                this.$http.MergePdf(this.id)
                this.topics = Object.assign(this.topics, res.data.topics)
                this.quillContent = false
                for (let i = 0; i < this.topics.length; i++) {
                  this.topics[i].officeUsers = this.officeUsersArr[i]
                }
                n === 'true' ? this.$router.push({
                  name: 'Conference'
                }) : '';
                return Promise.resolve(item)
              }))
          } else {
            this.topicEdit = false
          }

        })

        if (!this.topicEdit && n === 'false') {
          this.$message({
            type: 'error',
            message: this.$t('addConf.msgTip.two')
          })
          return
        }
        
        if(n === 'true') return;
        Promise.all(arr).then(res => {
          let topic = {
            title: '',
            content: '',
            type: '',
            attachments: [],
            department: '',
            officeUsers: [],
            supervisor: '',
            publisher: '',
            status: 3,
            votes: [{
              title: '',
              type: '',
              options: []
            }]
          }
          this.topics.push(topic)
          if (n === 'false') {
            // 锚点跳转
            this.allMsgScrollTop('议题')
            setTimeout(() => {
            	this.showToolsInfo()
            }, 200)
          }

        })
      },
      
      // 更新议题
      UpDataTopic(topic, val, index) {
        if (topic.type !== this.$t('addTopic.typeTd.research') && val === 2) {
          topic.votes = []
          let vote = {
            title: '',
            type: '',
            options: []
          };
          topic.votes.push(vote);
        } 

        if (val === 1) {
          let id = ''
          this.officeInfo.forEach(item => {
            if (item.name === topic.department) {
              id = item._id
            }
          })
          this.$http.GetGroupUsers(id)
            .then(res => {
              topic.officeUsers = res.data.list
              this.$set(this.topics, index, this.topics[index])
              if (res.data.list.length) return;
              this.$message({
                type: 'error',
                message: this.$t('addConf.msgTip.three')
              })
            })
        }

        if (!topic._id) return;
        this.imgChange(index)
        if(!this.topicEdit) return; 
        this.$http.UpdateTopic(topic._id, topic)
          .then(res => {
             this.quillContent = false
            this.$http.MergePdf(this.id)
          })
      },

      // 删除议题
      deleteTopic(topics, topic, idx) {
        if (topic._id) {
          this.$http.DelTopic(this.id, {
              topic: topic._id
            })
            .then(res => {
              topics.splice(idx, 1)
              this.officeUsersArr.splice(idx, 1)
              this.$http.MergePdf(this.id)
            })
        } else {
          topics.splice(idx, 1)
        }

      },

      // 控制滚动条
      allMsgScrollTop(type) {
        setTimeout(() => {

          type === '议题' ? this.topicIdx = this.$refs.topicBox.length - 1 : ''

          if (this.topicIdx !== this.$refs.topicBox.length - 1) return;

          if (type === '议题') {
            this.$refs.allMsg.scrollTo(0, this.$refs.topicBox[this.topicIdx].offsetTop);
          } else {
            this.$refs.allMsg.scrollTo(0, this.$refs.allMsg.scrollHeight);
          }

        }, 200);
      },

      // ==================================议题投票处理======================================
      addVote(topic, index) {
        let vote = {
          title: '',
          type: '',
          options: []
        };
        topic.votes.push(vote);
        this.$set(this.topics, index, this.topics[index])
        this.allMsgScrollTop('投票')
      },

      addOption(vote) {
        let option = {
          title: ''
        };
        if (!vote.options) {
          vote.options = []
        }

        vote.options.push(option);
        this.allMsgScrollTop('投票选项')
      },

      // 删除投票
      removeVote(topic, data, index) {
        data.splice(index, 1)
        this.UpDataTopic(topic, null, index)
      },

      // =================人员信息=========================
      userInfo(user) {
        if (!this.recipients || !this.officialMeeting.personnel) {
          user.forEach((item) => {
            this.recipients.push(item.name)
            this.officialMeeting.personnel.push(item._id)
          })
          return;
        }
        let names = [];
        let nameID = [];
        user.forEach((item) => {
          names.push(item.name)
          nameID.push(item._id)
        })
        for (let i = 0; i < names.length; i++) {
          this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0;
          this.officialMeeting.personnel.indexOf(nameID[i]) === -1 ? this.officialMeeting.personnel.push(nameID[i]) : 0;
        }
      },
      delUser(name, index) {
        this.recipients.splice(index, 1)
        this.officialMeeting.personnel.splice(index, 1)
        this.$refs.userData.deleteUser(index)
      },

      // ==================上传附件=========================
      onStatus(file) {
        if (file.isSuccess) {
          return this.$t('uploading.state.success');
        } else if (file.isError) {
          return this.$t('uploading.state.fail');
        } else if (file.isUploading) {
          return this.$t('uploading.state.uploading');
        } else {
          return this.$t('uploading.state.wait');
        }
      },
      onAddItem(files) {

        if (this.filtersFile) return;
        this.bol = true;
        this.allUploadCtrl = this.$t('uploading.state.confirm');
        if (!this.index) {
          this.tempFiles = files
        }
        this.tempFiles = files.slice(this.index)
        this.index = files.length;
      },
      onChange(index) {
        this.topicIndex = index;
        if (this.filtersFile) {
          this.$notify({
            title: this.$t('uploading.warn.title'),
            message: this.$t('uploading.warn.message'),
            type: 'warning'
          });
          this.filtersFile = false
          this.bol = false;
          this.index = 0
          this.$refs.vueFileUploader[this.topicIndex].fileUploader.queue = []
        }

      },
      btn(file) {
        return `width : ${file.progress * 1.5}px`;
      },
      fileCtrl() {
        this.bol = false;
      },
      uploadAll() {
        if (this.allUploadCtrl === this.$t('uploading.state.completed')) return;
        let p = Promise.resolve('ok');
        this.tempFiles.forEach(file => {
          p = p.then(res => {
            return this.uploadItem(file);
          })
        });
        p.then((data) => {
          this.allUploadCtrl = this.$t('uploading.state.completed')
          this.bol = false;
        })

      },
      uploadItem(file) {
        return new Promise((resolve, reject) => {
          file.upload(() => {
            setTimeout(() => {
              resolve('xzwen')
            }, 500);
          });
        });

      },

      // 删除附件
      delFile(files, idx, index) {

        this.topics[index].attachments.splice(idx, 1)
        this.$set(this.topics, index, this.topics[index])
        this.UpDataTopic(this.topics[index], null, index)
      },

      // 科室&&人员
      getOfficeInfo() {
        if (!this.officeList) return
        this.officeInfo = []
        if (this.officeList.parent !== null && !this.officeList.children.length) {
          this.officeInfo.push(this.officeList)
        } else if (this.officeList.parent !== null && this.officeList.children.length) {
          this.officeInfo = this.officeList.children
        } else if (this.officeList.parent === null && !this.officeList.children.length) {
          this.officeInfo.push(this.officeList)
        } else {
          this.officeInfo = this.officeList.children
        }
      },
      
			//	显示工具栏信息
			showToolsInfo(){
				for(let item of document.querySelectorAll('.ql-toolbar>.ql-formats')){
					for(let i = 0; i < item.childNodes.length; i++){
						item.childNodes[i].title = this.toolsInfoBack[i]
					}			
				}
      },
      
      // 
      imgChange(index){
        let aa = document.querySelectorAll('.ql-container>.ql-editor')[index]
        if(!aa.querySelectorAll('img')) return;
		    	for(let ele of aa.querySelectorAll('img')){
							if(ele.width > 540){
								this.topicEdit = false
								this.$notify({title: this.$t('messageTip.images.warn'), message: this.$t('messageTip.images.message'), type: 'warning'})
							}else{
                this.topicEdit = true
              }
						} 
      }
    },
    watch: {
      topics: {
        handler: function (val, oldVal) {},
        deep: true
      },

      officialMeeting: {
        handler: function () {
          this.listenInfo = true
        },
        deep: true
      },

      officeList() {
        this.getOfficeInfo()
      }
    },
    created() {
      this.getOfficeInfo()
      // 人员信息====》主讲人
      this.$http.GetPersonnelList()
        .then(res => {
          this.hosthostList = res.data
        })

      if (!this.$route.query.meetingID) return;
      this.$http.GetMeeting(this.$route.query.meetingID)
        .then(res => {
          this.officialMeeting.personnel = res.data.personnel.map((item) => {
            return item._id;
          })

          this.officialMeeting.title = res.data.title
          res.data.personnel.forEach(item => {
            this.recipients.push(item.name)
          })
        })
    }
  }

</script>



