<template>
    <transition name="fade">
            <div class="addpersonnelList" v-if = "personnelList">
                <h4>{{$t('personnelList.personnelList')}}</h4>
                <span role="button" @click="$store.dispatch('allclear')">&times;</span>
                <div class="personnelList-search">
                    <el-input class="personnelListKey" @change="getUser" @keydown.enter.native="personnelListKey" placeholder="请输入搜索人员" icon="search" v-model="searchUsers" :on-icon-click="personnelListKey">
                    </el-input>
                </div>
                <div class="addpersonnelList-allCheck">
                    <el-checkbox v-model="ChcekAll" @change="allChcek">全选</el-checkbox>
                </div>
                <el-radio-group v-model="personnelInfo" v-if="selectBox" @change="slectCheckBox">
                        <el-radio :label="user._id" v-for="user in personnelMsg" :key="user._id">{{user.name}}</el-radio>
                </el-radio-group>
                <el-checkbox-group v-model="personnelInfo" v-if="!selectBox" @change="slectCheckBox">
                        <el-checkbox :label="user" v-for="user in personnelMsg" :key="user._id">{{user.name}}</el-checkbox >
                </el-checkbox-group>
                
                <button type="button" class="btn btn-info btn-section" @click="changeMeetingStatus(topics)">{{$t('personnelList.confirm')}}</button>
            </div>
    </transition>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'
    import './personnelList.scss'
    export default {
        name:'personnelList',
        props:['topics', 'linkman'],
        computed:{
            ...mapGetters([
                'personnelList',
                'selectBox'
            ])
        },
        data(){
            return {
                personnelInfo:[],
                personnelMsg:[],
                searchUsers:'',
                ChcekAll: false
            }
        },
        methods: {
            allChcek(){
                this.ChcekAll ? this.personnelInfo = this.personnelMsg : this.personnelInfo = []
            },
            slectCheckBox(){
                this.personnelInfo.length === this.personnelMsg.length ? this.ChcekAll = true : this.ChcekAll = false
            },
            // 封装弹窗消息
            notification(title, msg, type){
                this.$notify({
                    title: title,
                    message: msg,
                    type: type
                });
            },
            // 封装请求人员数据
            GetPersonnelList(){
                this.$http.GetPersonnelList()
                .then(res =>{
                    this.ChcekAll = false
                    this.personnelMsg = res.data
                })
            },
            changeMeetingStatus(topics){
                let report = {
                    topics:topics,
                    confirmor:this.personnelInfo
                }

                let condition = false
               
                if(!this.personnelInfo.length){
                    this.notification('警告', '请选择人员', 'warning')
                    return;
                }
                
                if(this.$route.query.meetingID && topics && this.personnelInfo.length){

                    this.$http.UpdateMeetingStatus(this.$route.query.meetingID, report)
                        .then(res => {
                            this.$emit('child', res.data.status)
                            this.$store.dispatch('allclear')
                    })
                }else if(this.linkman && this.linkman.length && this.personnelInfo.length){
                    // AddlinkMan
                    for(let i = 0; i < this.linkman.length; i++){
                         
                        for(let j = 0; j < this.personnelInfo.length; j++){
                            if(this.linkman[i].UserId === this.personnelInfo[j]._id){
                                condition = true
                                break;
                            }
                        }
                    }

                    if(condition){
                        this.notification('警告', '不能添加已存在人员', 'warning')
                        condition = false
                    }else{
                        this.$emit('userInfo', this.personnelInfo)
                        this.$store.dispatch('allclear')
                    }
                    this.personnelInfo = []
                    
                }else{
                    this.$emit('userInfo', this.personnelInfo)
                    this.$store.dispatch('allclear')
                    this.personnelInfo = []
                }
                
            },

            // delelet人员
            deleteUser(index) {
                this.personnelInfo.splice(index, 1)
                this.$emit('userInfo', this.personnelInfo)
            },

            personnelListKey(){
                if(!this.searchUsers) return;
                this.$http.SearchUser(this.searchUsers)
                    .then(res =>{
                        this.personnelMsg = res.data
                    })
            },
            getUser(){
                if(this.searchUsers) return;
                this.GetPersonnelList()
            }
        }
    }
</script>


