<template>
	<li v-if="node.name" class="treeNode">
		<div class="node-scope">
			<div @click.stop="treeLi(node._id)" :class="{treeHandle:node.children.length?chevron :'', 'treeActiveStyle':$route.params.id===node._id}" role='button'>
				<p class="group-icon"></p>
				<span v-if="changeTitle" class="treeName" @dblclick.stop="UpDataTitle('1', node.name)">{{ node.name }}</span>
				<input v-else type="text" v-model="nodeName" class="changeName" v-focus @blur="UpDataTitle('2', nodeName)" @keydown.enter.stop="UpDataTitle('2', nodeName)">
				<div class="treeArrows pull-right" v-if="node.children.length" @click.stop="treeToggle(node, index)">
					<span><i class="fa"  v-bind:class="chevron?'fa-chevron-down':'fa-chevron-right'"></i></span>
				</div>
				<div class="btnHandle pull-right">
					<button type="button" class="btn btn-info" @click.stop="getInformation(node, index)" v-if="jurisdiction"><i class="fa fa-plus"></i></button>
					<button type="button" class="btn btn-danger" @click.stop="treeRemove(node, index)" v-if="jurisdiction&&node.parent!==null"><i class="fa fa-minus"></i></button>
				</div>
			</div>
			<ol v-show="chevron">
				<tree v-if="node.children.length" v-for="(row, idx) in node.children" :key="idx" :node="row" :index="idx"></tree>
			</ol>
		</div>
	</li>
</template>
<script>
	import DirList from '../packageJS/dirList.js'
	import { mapActions, mapGetters } from 'vuex'
	import './myTree.scss'
	export default {
		name: 'tree',
		props: ['node', 'index'],
		computed: {
			...mapGetters([
				'jurisdiction',
				'sideData'
			])
		},
		data() {
			return {
				chevron: false,
				changeTitle: true,
				groupsId: null,
				nodeName: null,
				oldName: null
			}
		},
		methods: {
			// 编辑标题
			UpDataTitle(num, val) {
				if(num === '1') {
					this.oldName = val
					this.nodeName = val
					this.changeTitle = false
				} else {
					if(this.oldName === val) {
						this.changeTitle = true
					} else {
						this.$http.ChangeGroups(this.groupsId, {
								name: val
							})
							.then(res => {
								this.$http.GetGroups()
									.then((data) => {
										this.$store.dispatch('createGroup', data.data)
										let treesInfo = DirList.GetDirList(this.sideData, this.groupsId)
										this.$store.dispatch('showPaths', treesInfo)
										this.$message({
											message: "编辑成功",
											duration: 2000,
											type: 'success'
										});
										this.changeTitle = true
									})
							})
					}

				}

			},
			getInformation(node, index) {
				this.$store.dispatch('getInformation', node.name)
				this.$store.dispatch('getParentInfo', node)
			},
			treeLi(id) {
				this.groupsId = id
				this.$router.push({
					path: `/${id}/`
				})

				let treesInfo = DirList.GetDirList(this.sideData, id)
				this.$store.dispatch('showPaths', treesInfo)
			},
			// 删除节点
			treeRemove(node, index) {
				this.$confirm(this.$t('messageTip.msgBox.confirm'), this.$t('messageTip.msgBox.tip'), {
					confirmButtonText: this.$t('messageTip.msgBox.confirmText'),
					cancelButtonText: this.$t('messageTip.msgBox.cancelText'),
					type: 'warning'
				})
				.then(() => {
					return this.$http.DeleteGroups(node._id)
				})
				.then((data) => {
					this.$message({
						type: 'success',
						message: this.$t('messageTip.deleteSuccess')
					});
					return this.$http.GetGroups()
				})
				.then((data) => {
					this.$store.dispatch('createGroup', data.data)
					//	group无数据清除所有，有数据但id和group中任意一个id不匹配表示已删除则默认回到第一个
					if(!data.data.length) {
						this.$router.push({
							path: '/'
						})
						this.$store.dispatch('showPaths', [])
					}else {
						let treesInfo = DirList.GetDirList(this.sideData, this.$route.params.id)
						if(!treesInfo.length) {
							this.$router.push({
								path: `/${data.data[0]._id}/`
							})
							this.$store.dispatch('showPaths', [data.data[0]])
						}
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: this.$t('messageTip.cancelDelete')
					});
				});
			},
			// 菜单开关
			treeToggle(node, index) {
				this.chevron = !this.chevron
			}
		}
	}
</script>


