<template>
    <div class="sendInform">
          <div class="sendInform-header">
            <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <div class="sendInform-handle">
              <el-button @click='publishInform'>{{$t('sendInform.publish')}}</el-button>
            </div>
          </div>
          <div class="sendInformTable">
            <form action="" method="get">
              <table class="sendInformTable-table">
                <tr>
                  <td>{{$t('sendInform.title')}}</td>
                  <td>
                    <input type="text" class="sendInformTable-table-theme" v-model="sendInformTitle"/>
                  </td>
                </tr>
                <tr>
                  <td>{{$t('sendInform.receiver')}}</td>
                  <td>
                      <el-tag :key="index" type="primary" v-for="(item, index) in recipients" :closable="true" :close-transition="false" @close="delUser(item, index)" class="sendInform-el-tag">
                          {{item.name}}
                      </el-tag> 
                      <span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="sendInformTable-table-add">{{$t('sendInform.recevierTd.selectStaff')}}</span></span>                    
                  </td>
                </tr>
                <tr>
                  <td>{{$t('sendInform.content')}}</td>
                  <td>
                    <quill-editor v-model="sendInformContent" :options="editorOption"></quill-editor>
                  </td>
                </tr>
              </table>
            </form>
        </div>
        <personnel @userInfo = "userInfo" ref="userData"></personnel>
    </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import personnel from '../personnelList/personnelList'
import './sendInform.scss'
export default {
  	name: 'sendInform',
	components: {
  		personnel
	},
	computed: {
		...mapGetters(['toolsInfoBack'])
	},	
  	data() {
      	return {
	        sendInformChecked: [],
	        sendInformTitle:'',
	        sendInformContent:'',
	        recipients:[],
	        editorOption: {
	          	placeholder: '',
	          	modules: {
	            	toolbar: [
		            	[
		              		'bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block',
		              		{'header': 1}, {'header': 2}, {'list': 'ordered'}, {'list': 'bullet'},
							{'script': 'sub'}, {'script': 'super'}, {'direction': 'rtl'}, {'align': 'center'},
							'clean', 'link', 'image'
		            	]
	            	],
	            	imageResize: {
		              	displayStyles: {
		                	backgroundColor: '#000',
		                	border: 'none',
		                	color: 'white'
		              	}
	            	}
	          	}
	        }
      	}
	},
	methods: {
	    userInfo(user){
        	if (!this.recipients.length) {
        		for(let item of user){
        		    this.recipients.push({name: item.name, _id: item._id})
        		}
        	} else {
        		let tempArr = this.recipients
        		let num = 0
        		for(let item of user){
        			num = 0
        			for(let ele of tempArr){
        				if(ele._id === item._id){
							num++
							break;
        				}
        			}
        			if(!num){
        				this.recipients.push({name: item.name, _id: item._id})
        			}
        		}
        	}     	
	    },
	    publishInform(){
			//	限制图片大小
			for(let ele of document.querySelectorAll('.ql-container>.ql-editor img')){
				if(ele.width > 540){
					this.$notify({title: this.$t('messageTip.images.warn'), message: this.$t('messageTip.images.message'), type: 'warning'})
					return;
				}
			}	    	
	        if(!this.sendInformTitle || !this.recipients.length || !this.sendInformContent){
	          this.$message({
	            type: 'error',
	            message: this.$t('inform.msg')
	          }) 
	          return
	        }
	        let arrId = this.recipients.map(item => {
	        	return item._id
	        })
	    	let publishList = {
				title : this.sendInformTitle,
				target : arrId,
				content : {
					title : this.sendInformTitle,
					content : this.sendInformContent,
					type : "text"
				}
			}
	    	this.$http.sendInform(publishList)
	    	.then((data) => {
	    		this.$router.push({path:'Inform'})
	    	})
	    	.catch((err) => {
	    		console.log(err)
	    	})
	    },
	    delUser(item, index) {
	        this.recipients.splice(index, 1)
	    },
		//	显示工具栏信息
		showToolsInfo(){
			let tabBarArr = document.querySelectorAll('.ql-toolbar>.ql-formats>button')
			for(let i = 0; i < tabBarArr.length; i++){
				tabBarArr[i].title = this.toolsInfoBack[i]
			}			
		}	    
  	},
	mounted(){
		this.showToolsInfo()
	}
}

</script>


