// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router/index'
import store from './vuex'
import VueQuillEditor, { Quill } from 'vue-quill-editor'
import ImageResize from 'quill-image-resize-module'
import './main.scss'
import api from './api/api.js'
import ElementUI from 'element-ui'
import paper from 'paper'
import VueI18n from 'vue-i18n'
import io from 'socket.io-client'
import pdf from 'pdfjs-dist'
import configure from './configure'
import 'quill/dist/quill.snow.css'
// import pdff from 'pdfobject'
Vue.use(VueI18n)
Vue.use(ElementUI)
Vue.use(VueQuillEditor)

paper.install(window)
Vue.prototype.$http = api
Vue.prototype.pdf = pdf
Vue.config.productionTip = false

import Chi from './language/zh/zh'
import Eng from './language/en/en'
import Zht from './language/zh-hant/zh-hant'
const i18n = new VueI18n({
    locale: 'zh-hant',    // 默认语言
    messages: {
        'zh-CN': Chi,   // 中文语言包
        'en-US': Eng,   // 英文语言包
        'zh-hant': Zht  // 中文繁體包
    }
})

let routerArray = []
let num = 0
router.beforeEach((to, from, next) => {
  	// 对 to.matched 数组中的每个路由调用箭头函数
  	if (to.meta.requireAuth) {
	    // 判断登录状态
	    if (window.localStorage.getItem('token')) {
	      	// 继续路由
	      	// tree显示路由
			let treeTemp = store.state.treeNode.treeName
		   	if(num){
		   		treeTemp.pop()
		   		num--
		   	}
		   	let lang = window.localStorage.getItem('language')
		   	if(lang === 'en-US'){
		   		routerArray = Eng.allRouter[0]
		   	}else if(lang === 'zh-CN'){
		   		routerArray = Chi.allRouter[0]
		   	}else{
		   		routerArray = Zht.allRouter[0]
		   	}
		   	if(!(to.name === 'stystem' || to.name === 'ChildRouter')){
				for(let key in routerArray){
	   				if(to.name === key){
						treeTemp.push({
							name : routerArray[key]
						})
						break;
	   				}
				}
				num++
		  	}
		   	store.state.treeNode.treeName = treeTemp
	      	next()
	    } else {
	      	// 重定向到登录界面
	      	next({
	        	path: '/Login',
	        	query: { redirect: to.fullPath }
	      	})
	    }
  	} else {
    	// 继续路由
    	next()
  	}
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: {
      App
    }
})



