<template>
	<div class="addLinkman-page">
		<div class="addLinkman-header">
			<div class="addLinkman-header-back" @click="$router.back()">
				<i class="fa fa-angle-left fa-2x"></i>
				<span>{{$t('pageCommon.back')}}</span>
			</div>
			<div>
				<button class="add-btn" @click="createWindow">{{$t('staffPage.create')}}</button>
				<button class="add-btn" @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')">{{$t('staffPage.add')}}</button>
			</div>
		</div>
		<div class="addLinkman-table">
            <table class="table table-striped table-hover">
                <thead>
                    <tr class="row">
                        <th class="col-md-1">{{$t('staffPage.prop.num')}}</th>
                        <th class="col-md-1">{{$t('staffPage.prop.loginName')}}</th>
                        <th class="col-md-1">{{$t('staffPage.prop.userName')}}</th>
                        <th class="col-md-1">{{$t('staffPage.prop.role')}}</th>
                        <th class="col-md-2">{{$t('staffPage.prop.phone')}}</th>
                        <th class="col-md-2">{{$t('staffPage.prop.devices')}}</th>
                        <th class="col-md-2">{{$t('staffPage.prop.department')}}</th>
                        <th>{{$t('staffPage.prop.Management')}}</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(data, index) in tableData" class="row" :key="index">
                        <td class="col-md-1">{{(index + 1) + pageSize*(currentPage - 1)}}</td>
                        <td class="col-md-1">
							{{data.loginName}}
                        </td>
                        <td class="col-md-1">{{data.userName}}</td>
                        <td class="col-md-1">{{data.role}}</td>
                        <td class="col-md-2">{{data.phone}}</td>
                        <td class="col-md-2">{{data.devices}}</td>
                        <td class="col-md-2">{{data.department}}</td>
                        <td>
                            <el-button class='addLinkman-table-editor' @click="editorUser(data)">{{$t('pageCommon.editorText')}}</el-button>
							<el-button class='addLinkman-table-delete' @click="deleteRow(data)">{{$t('pageCommon.deleteText')}}</el-button>
                        </td>
                    </tr>
                </tbody>
            </table>
    	</div>
		<el-pagination
			mini
			layout="prev, pager, next"
			:current-page.sync="currentPage"
			:page-size='pageSize'
			:total="counts" @current-change="currentPageChange"
			class='addLinkman-pagination' v-if="counts>30">
		</el-pagination>		
		
	    <transition name="show">
	      	<div class="addLinkman-shade" v-if="createUserCtrl" @click.stop="addLinkmanShadow">
		      	<div class="addLinkman-window" v-if='addUserCtrl' @click.stop="createUserCtrl=true">
		        	<div class="add-addLinkman-top">
		          		<span>{{$t('staffPage.form.addUser')}}</span>
		          		<i class="fa fa-close" @click.stop="closeBtn"></i>
		        	</div>
		        	<el-form :model="userMsgList" ref="userMsgList" :rules='createRules'>
			        	<div class="add-addLinkman-info">
			            	<p>{{$t('staffPage.form.loginName')}}</p>
							<el-form-item prop="loginName">
								<el-input type="text" v-model='userMsgList.loginName' auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.loginText')"></el-input>
							</el-form-item>			        		
			            	<p>{{$t('staffPage.form.UserName')}}</p>
							<el-form-item>
								<el-input type="text" v-model='userMsgList.name' auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.UserText')"></el-input>
							</el-form-item>	

							<p>{{$t('staffPage.form.role')}}</p>
							<el-form-item>
								<el-select v-model="userMsgList.roleName" :placeholder="$t('staffPage.form.placeHolder.roleTexts')">
									<el-option
										v-for="item in userTypes"
										:key="item.value"
										:label="item.label"
										:value="item.value">
									</el-option>
								</el-select>
							</el-form-item>	

			            	<p>{{$t('staffPage.form.email')}}</p>
							<el-form-item prop="email">
								<el-input type="text" v-model="userMsgList.email" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.emailText')"></el-input>
							</el-form-item>	
			            	<p>{{$t('staffPage.form.phone')}}</p>
							<el-form-item prop="phone">
								<el-input type="text" v-model="userMsgList.phone" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.phoneText')"></el-input>
							</el-form-item>	
			            	<p>{{$t('staffPage.form.mac')}}</p>
							<el-form-item>
								<el-input type="text" v-model="userMsgList.Mac" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.macText')"></el-input>
							</el-form-item>
			            	<p>{{$t('staffPage.form.password')}}</p>
							<el-form-item prop="pwd">
								<el-input type="password" v-model="userMsgList.pwd" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.pwdText')"></el-input>
							</el-form-item>	
			            	<p>{{$t('staffPage.form.confirmPwd')}}</p>
							<el-form-item prop="checkPwd">
								<el-input type="password" v-model="userMsgList.checkPwd" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.confirmPwdText')"></el-input>
							</el-form-item>								
			        	</div>
			        	<div class="add-addLinkman-btn">
							<el-form-item>
						    	<el-button @click="createUser('userMsgList')" class='addLinkman-create-btn'>{{$t('staffPage.form.create')}}</el-button>
							</el-form-item>			          		
			        	</div>
			        </el-form>
		      	</div>
		      	
		      	<div class="addLinkman-window addLinkman-editor-window" v-if='editorUserCtrl' @click.stop="createUserCtrl=true">
		        	<div class="add-addLinkman-top">
		          		<span>{{$t('staffPage.form.editorUser')}}</span>
		          		<i class="fa fa-close" @click.stop="closeBtn"></i>
		        	</div>
		        	<el-form :model="userMsgList" ref="userMsgList" :rules='createRules'>
			        	<div class="add-addLinkman-info">
			            	<p>{{$t('staffPage.form.loginName')}}</p>
							<el-form-item prop="loginName">
								<el-input type="text" v-model='userMsgList.loginName' auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.loginTexts')"></el-input>
							</el-form-item>			        		
			            	<p>{{$t('staffPage.form.UserName')}}</p>
							<el-form-item>
								<el-input type="text" v-model='userMsgList.name' auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.UserText')"></el-input>
							</el-form-item>
			            	<p>{{$t('staffPage.form.role')}}</p>
							<el-form-item>
								<el-select v-model='userMsgList.roleName' :placeholder="$t('staffPage.form.placeHolder.roleTexts')">
									<el-option
										v-for="item in userTypes"
										:key="item.value"
										:label="item.label"
										:value="item.value">
									</el-option>
								</el-select>
							</el-form-item>							
			            	<p>{{$t('staffPage.form.email')}}</p>
							<el-form-item prop="email">
								<el-input type="text" v-model="userMsgList.email" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.emailText')"></el-input>
							</el-form-item>							
			            	<p>{{$t('staffPage.form.phone')}}</p>
							<el-form-item prop="phone">
								<el-input type="text" v-model="userMsgList.phone" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.phoneTexts')"></el-input>
							</el-form-item>
			            	<p>{{$t('staffPage.form.mac')}}</p>
							<el-form-item>
								<el-input type="text" v-model="userMsgList.Mac" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.macText')" disabled></el-input>
							</el-form-item>
			            	<!--<p>{{$t('staffPage.form.password')}}</p>
							<el-form-item prop="pwd">
								<el-input type="password" v-model="userMsgList.pwd" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.pwdTexts')"></el-input>
							</el-form-item>	
			            	<p>{{$t('staffPage.form.confirmPwd')}}</p>
							<el-form-item prop="checkPwd">
								<el-input type="password" v-model="userMsgList.checkPwd" auto-complete="off" :placeholder="$t('staffPage.form.placeHolder.confirmPwdTexts')"></el-input>
							</el-form-item>							-->
			        	</div>
			        	<div class="add-addLinkman-btn">
							<el-form-item>
						    	<el-button @click="SaveSend('userMsgList')" class='addLinkman-create-btn'>{{$t('staffPage.form.save')}}</el-button>
							</el-form-item>			          		
			        	</div>
		        	</el-form>
		      	</div>
	      	</div>      		      	
	    </transition>
	    <personnel @userInfo = "userInfo" ref="userData" :linkman = "tableData"></personnel>
	</div>
</template>

<script>
	import {mapActions, mapGetters} from 'vuex'
	import personnel from '../personnelList/personnelList'
	import './addLinkman.scss'
    export default {
        name:'AddLinkman',
        components: {
            personnel
        },        
		data() {
			// 验证登录名
	      	var checkLoginName = (rule, value, callback) => {
		        if (!value) {
		          	return callback(new Error(this.$t('staffPage.form.checkForm.login')));
		        }
		        callback();
	    	};			
	    	// 验证手机号码
	      	var checkPhone = (rule, value, callback) => {
		        if (!value) {
		          	return callback(new Error(this.$t('staffPage.form.checkForm.noPhone')));
		        }
		        setTimeout(() => {
			        if (!(/^1[34578]\d{9}$/.test(value))) {
			            callback(new Error(this.$t('staffPage.form.checkForm.phone')));
			        }else{
		              	callback();
		           }
		        }, 1000);
	    	};
	    	// 验证邮箱
	      	var checkEmail = (rule, value, callback) => {
		        if (!value) {
		          	return callback(new Error(this.$t('staffPage.form.checkForm.noEmail')));
		        }
		        setTimeout(() => {
			        if (!(/^([a-zA-Z0-9_-]){2,}@([a-zA-Z0-9_-]){2,}((\.[a-zA-Z0-9_-]{2,3}){1,2})$/).test(value)) {
			            callback(new Error(this.$t('staffPage.form.checkForm.correctEmail')));
			        }else{
		              	callback();
		           }
		        }, 1000);
	    	};	    	
	    	// 验证密码
	      	var validatePass = (rule, value, callback) => {
	        	if (value === '') {
	          		callback(new Error(this.$t('staffPage.form.checkForm.noPwd')));
	        	} else {
	          	if (this.userMsgList.checkPwd !== '') {
	            	this.$refs.userMsgList.validateField('checkPwd');
	          	}
	          		callback();
	        	}
	      	};
	      	var validatePass2 = (rule, value, callback) => {
	        	if (value === '') {
	        		callback(new Error(this.$t('staffPage.form.checkForm.checkPwd')));
	        	} else if (value !== this.userMsgList.pwd) {
	          		callback(new Error(this.$t('staffPage.form.checkForm.inconsistentPwd')));
	        	} else {
	          		callback();
	        	}
	      	};      	
			return {
                pageSize : 30,
                currentPage: 1,
                counts: 0,
                
				tableData: [],
				createUserCtrl : false,
				editorUserCtrl : false,
				addUserCtrl : false,
				userId : '',
				ReqList: {
				  	where: {
				      	offset: 0,
				      	limit: 30,
				      	sortBy: 'createdAt',
				      	order: -1
				    }
				},
		        userMsgList : {
		        	email : '',
		        	Mac : '',
		        	name : '',
		        	phone : '',
		        	loginName : '',
		        	pwd : '',
		        	checkPwd : '',
		        	roleName : ''		        	
		        },
		        createRules: {
					phone : [
						{ validator: checkPhone, trigger: 'blur' }
					],
					loginName : [
						{ validator: checkLoginName, trigger: 'blur' }
					],
					email : [
						{ validator: checkEmail, trigger: 'blur' }
					],
					pwd: [
						{ validator: validatePass, trigger: 'blur' }
					],
					checkPwd: [
						{ validator: validatePass2, trigger: 'blur' }
					]				
				},
				userTypes: [{
					value: 'user',
					label: this.$t('staffPage.userTypes.user')
					}, {
					value: 'admin',
					label: this.$t('staffPage.userTypes.admin')
					}, {
					value: 'aide',
					label: this.$t('staffPage.userTypes.aide')
				}]
		   }
		},
		methods : {
			//	删除目标栏
			deleteRow(row) {
		        this.$confirm(this.$t('messageTip.msgBox.confirm'), this.$t('messageTip.msgBox.tip'), {
		            confirmButtonText: this.$t('messageTip.msgBox.confirmText'),
		            cancelButtonText: this.$t('messageTip.msgBox.cancelText'),
		            type: 'warning'
		        }).then(() => {
					this.$http.DeleteGroupsUser(this.$route.params.id, row.childId)
						.then(data=>{
                        	let tempoffset = this.ReqList.where.offset
                        	let tempcounttwo = (this.counts) / tempoffset
                        	let tempcountone = (this.counts - 1) / tempoffset
                        	if(tempoffset !== 0 && (Math.ceil(tempcounttwo) === this.currentPage) && ((tempcountone) % 1 === 0)){
                        		this.currentPage -= 1
                        	}else{
                        		this.getListData()
                        	}							
						})
		            this.$message({
		              type: 'success',
		              message: this.$t('messageTip.deleteSuccess')
		            });		        	
		        }).catch(() => {
		        	this.$message({
		        	  	type: 'info',
		        	  	message: this.$t('messageTip.cancelDelete')
		        	});		        	
		        });
		    },
		    //	编辑默认数据显示
			editorUser(row){
				this.editorUserCtrl = true
				this.createUserCtrl = true
				this.userId = row.UserId
				this.userMsgList.loginName = row.loginName
				this.userMsgList.name = row.userName
				this.userMsgList.roleName = row.role
				this.userMsgList.email = row.email
				this.userMsgList.phone = row.phone
				this.userMsgList.Mac = row.mac
			},			
			//  关闭弹窗全部初始化
			closeBtn(){
				this.createUserCtrl = false
				this.editorUserCtrl = false
				this.addUserCtrl = false
				this.userMsgList.email = ''
				this.userMsgList.name = ''
				this.userMsgList.phone = ''
				this.userMsgList.Mac = ''
				this.userMsgList.roleName = ''
				this.userMsgList.loginName = ''
				this.userMsgList.pwd = ''
				this.userMsgList.checkPwd = ''
			},
			//	添加人员
            userInfo(user){
            	for(let item of user){
            		this.$http.AddGroupsToUser(this.$route.params.id, {child : item._id})
            		.then(data=>{
            			this.getListData()
            			this.$message({message : this.$t('messageTip.addSuccess'), duration : 2000, type: 'success'})
            		})
            	}	      		            	               
            },
            //	打开创建弹窗
			createWindow(){
				this.createUserCtrl = true
				this.addUserCtrl = true
			},
			// 创建保存
			createUser(formName){
		        this.$refs[formName].validate((valid) => {
		        	if (!valid) {
				        this.$message({message : this.$t('messageTip.createFail'), duration : 2000, type: 'error'})
				        return false;	            	
		        	}
					let sendUserMsg = {
						role: this.userMsgList.roleName,
						name : this.userMsgList.loginName,
			        	email : this.userMsgList.email,
			        	mac : this.userMsgList.Mac,
			        	password : this.userMsgList.pwd,
			        	info : {
			        		name : this.userMsgList.name,
			        		phone : this.userMsgList.phone
			        	}
					}
					this.$http.CreateUserByGroup(this.$route.params.id, sendUserMsg)
					.then(data=>{
						this.$message({message : this.$t('messageTip.createSuccess'), duration : 2000, type: 'success'})
						this.getListData()
						this.closeBtn()
					})
					.catch(err=>{
						let regName = new RegExp(/(name:)/, 'g')
						let regEmail = new RegExp(/(email:)/, 'g')
						if(err.response.data && regName.test(err.response.data.message)){
							this.$message({message : this.$t('staffPage.form.failTip.login'), duration : 2000, type: 'error'})
						}else if(err.response.data && regEmail.test(err.response.data.message)){
							this.$message({message : this.$t('staffPage.form.failTip.email'), duration : 2000, type: 'error'})
						}else{							
							this.$message({message : this.$t('messageTip.createFail'), duration : 2000, type: 'error'})
						}
					})
		        });				
			},
			//	关闭遮罩
			addLinkmanShadow(){
				this.closeBtn()
			},
			// 编辑保存
			SaveSend(formName){
		        this.$refs[formName].validate((valid) => {
		        	if (!valid) {
				        this.$message({message : this.$t('messageTip.editorFail'), duration : 2000, type: 'error'})
				        return false;	            	
		        	}
					let sendEditorMsg = {
						role : this.userMsgList.roleName,
						name : this.userMsgList.loginName,
			        	email : this.userMsgList.email,
			        	info : {
			        		name : this.userMsgList.name,
			        		phone : this.userMsgList.phone
			        	}
					}				
					this.$http.UpdateUserMsg(this.userId, sendEditorMsg)
					.then(()=>{
						this.$http.GetUserMsg()
						.then(data=>{
							window.localStorage.setItem('userInfo', escape(JSON.stringify(data.data)))
							this.$store.dispatch('jurisdiction', data.data)
						})						
						this.$message({message : this.$t('messageTip.createSuccess'), duration : 2000, type: 'success'})
						this.getListData()
						this.closeBtn()
					})
					.catch(err=>{
						let regName = new RegExp(/(name:)/, 'g')
						let regEmail = new RegExp(/(email:)/, 'g')
						if(err.response.data && regName.test(err.response.data.message.message)){
							this.$message({message : this.$t('staffPage.form.failTip.login'), duration : 2000, type: 'error'})
						}else if(err.response.data && regEmail.test(err.response.data.message.message)){
							this.$message({message : this.$t('staffPage.form.failTip.email'), duration : 2000, type: 'error'})
						}else{
							this.$message({message : this.$t('messageTip.editorFail'), duration : 2000, type: 'error'})
						}					
					})
				})					
			},
			//	获取列表数据
			getListData(){
	      		this.$http.GetGroupsUserList(this.$route.params.id, this.ReqList)
	      		.then((data)=>{
	      			let tempTableData = []
	      			for(let item of data.data.list){
	      				if(!item.child) continue;
	      				if(!item.child.devices.length){
	      					item.child.devices.push({mac : ''})
	      				}
	      				tempTableData.push({
	      					role : this.filtersRole(item.child.role),
	      					loginName : item.child.name,
	      					userName : item.child.info.name,
	      					phone : item.child.info.phone,
	      					childId : item._id,
	      					UserId : item.child._id,
	      					email : item.child.email,
	      					mac : item.child.devices[0].mac
	      				})	      				
	      			}	  
					this.tableData = tempTableData
	      			this.counts = data.data.count
	      		})
	      		.catch(error=>{
	      			console.log(error)
	      		})
			},
			filtersRole(value) {
				switch (value) {
					case 'admin':
						value = this.$t('staffPage.userTypes.admin')
						break;
					case 'aide':
						value = this.$t('staffPage.userTypes.aide')
						break;
					default:
						value = this.$t('staffPage.userTypes.user')
						break;
				}
				return value
			},			
			//	分页
			currentPageChange(val){
				this.ReqList.where.offset = (val - 1) * 30
				this.getListData()
			}
		},
		created(){
			this.getListData()
		}		
    }	
</script>


