<template>
    <div class="home">
        <el-row class="home-menu row">
            <!--<el-col class="home-topic col-sm-6 col-xs-12">
                <router-link tag="div" to="Topic">
                    <span></span>
                    <div class="home-title">
                        <h2>{{$t("homeTile.topic")}}</h2>
                        <p>{{$t("total")}}&nbsp;<span>{{homePageCount.meetingCount}}</span></p>   
                    </div>
                </router-link>
            </el-col>-->
            <el-col class="home-conference col-sm-6 col-xs-12">
                <router-link tag="div" to="Conference">
                    <span></span>
                    <div class="home-title">
                        <h2>{{$t("homeTile.meeting")}}</h2>
                        <p>{{$t("total")}}&nbsp;<span>{{homePageCount.offciaCount}}</span></p>   
                    </div>
                </router-link>
            </el-col>
            <!-- <el-col class="home-vote col-sm-6 col-xs-12">
                <router-link tag="div" to="Vote">
                    <span></span>
                    <div class="home-title">
                        <h2>{{$t("homeTile.vote")}}</h2>
                        <p>{{$t("total")}}&nbsp;<span>{{homePageCount.votesCount}}</span></p>   
                    </div>
                </router-link>
            </el-col> -->
            <el-col class="home-message col-sm-6 col-xs-12">
                <router-link tag="div" to="Inform">
                <span></span>
                <div class="home-title">
                    <h2>{{$t("homeTile.inform")}}</h2>
                    <p>{{$t("total")}}&nbsp;<span>{{homePageCount.noticeCount}}</span></p>   
                </div>
                </router-link>
            </el-col>
            <el-col class="home-crew col-sm-6 col-xs-12">
                <router-link tag="div" to="AddLinkman">
                <span></span>
                <div class="home-title">
                    <h2>{{$t("homeTile.staff")}}</h2>
                    <p>{{$t("total")}}&nbsp;<span>{{homePageCount.usersCount}}</span></p>   
                </div>
                </router-link>
            </el-col>
            <el-col class="home-facility col-sm-6 col-xs-12">
                <router-link tag="div" to="Device">
                <span></span>
                <div class="home-title">
                    <h2>{{$t("homeTile.device")}}</h2>
                    <p>{{$t("total")}}&nbsp;<span>{{homePageCount.devicesCount}}</span></p>   
                </div>
                </router-link>
            </el-col>
        </el-row>
    </div>
    
    
</template>

<script>
	import {
		mapActions,
		mapGetters
	} from 'vuex'	
    import './home.scss'
    export default {
        name:'home',
        data(){
        	return {
        		totalCount : {}
        	}
        },
		computed: {
			...mapGetters([
				'homePageCount'
			])
		},
		methods: {
			//	获取主页各模块的数量
			homeTotalCount(){
		      	this.$http.GetHomeNum(this.$route.params.id)
		      	.then(res=>{
		      		this.$store.dispatch('homeTotalCount', res.data)
		      	})
		      	.catch(err=>{
		      		console.log(err)
		      	})
			}
		},
        created(){
        	this.homeTotalCount()
        },
        watch: {
        	$route(newVal, oldVal){
        		if(newVal.params.id !== oldVal.params.id){
        			this.homeTotalCount()
        		}
        	}
        }
    }
</script>


