<template>
	<div class="inform-page">
		<div class="inform-header">
			<div class="infom-header-back" @click="backToHome">
				<i class="fa fa-angle-left fa-2x"></i>
				<span>{{$t('pageCommon.back')}}</span>
			</div>
			<router-link to='SendInform' tag='button' class="add-btn" v-if="permissionCtrl">{{$t('inform.Send')}}</router-link>
		</div>
		<el-table
			:data="tableData"
			stripe
			class='inform-table'>
			<el-table-column
				prop="num"
				:label="$t('inform.prop.num')"
				width="104">
			</el-table-column>
			<el-table-column
				prop="title"
				:label="$t('inform.prop.title')">
			</el-table-column>
			<el-table-column
				prop="time"
				:label="$t('inform.prop.date')">
			</el-table-column>			
			<el-table-column
				prop="type"
				:label="$t('inform.prop.type')">
			</el-table-column>
            <el-table-column
                :label="$t('inform.prop.Management')"
                width="160">
                <template scope="scope">
                    <el-button class='inform-table-editor' @click="viewInform(scope.row)">{{$t('inform.view')}}</el-button>
					<!--<el-button class='inform-table-delete'>{{$t('pageCommon.deleteText')}}</el-button>-->
                </template>
            </el-table-column>
		</el-table>
		<el-pagination
			mini
			layout="prev, pager, next"
			:current-page.sync="currentPage"
			:page-size='pageSize'
			:total="counts" @current-change="currentPageChange"
			class='inform-pagination' v-if="counts>30">
		</el-pagination>		
		
	    <transition name="checkInform">
	      	<div class="checkInform-shade" v-if="checkInformCtrl" @click.stop="checkInformCtrl=false">
		      	<div class="checkInform-window" @click.stop="checkInformCtrl=true">
		        	<div class="add-checkInform-top">
		          		<span>{{$t('inform.window.name')}}</span>
		          		<i class="fa fa-close fa-2x" @click.stop="checkInformCtrl=false" role='button'></i>
		        	</div>
		        	<div class="checkInform-content-wrap" v-show="typeCtrl">	
		        		<h4>{{$t('inform.window.title')}}</h4>
		        		<p>{{viewTitle}}</p>
		        		<h4>{{$t('inform.window.content')}}</h4>
		        		<div class="checkInform-content" v-html="viewContent"></div>
		        	</div>
		        	<div class="inform-option" v-show="!typeCtrl">
		        		<h4>{{$t('votePage.window.title')}}</h4>
		        		<p class="inform-option-title">{{voteMsg.title}}</p>
		        		<h4>{{$t('votePage.window.content')}}</h4>
  				  		<el-radio-group v-model="radioIndex" v-show="voteMsg.type===1">
		   					<el-radio v-for="(item, index) in voteMsg.options" :label="index" :key="item.title" class="inform-el-option">{{item.title}}
		   						<span class="option-inform-result">{{$t('votePage.window.result')}}：{{item.select.length}}</span>
		   					</el-radio>
  						</el-radio-group>			
					  	<el-checkbox-group v-model="checkboxIndex" v-show="voteMsg.type===2">
					    	<el-checkbox v-for="(item, index) in voteMsg.options" :label="index" :key="item.title" class="inform-el-option">{{item.title}}
					    		<span class="option-inform-result">{{$t('votePage.window.result')}}：{{item.select.length}}</span>
					    	</el-checkbox>
					  	</el-checkbox-group>
		        	</div>		        	
		      	</div>	      	
	      	</div>
	    </transition>
	</div>
</template>

<script>
	import './inform.scss'
    export default {
        name:'Inform',
		data() {
			return {
                pageSize : 30,
                currentPage: 1,
                counts: 0,				
				tableData: [],
				checkInformCtrl : false,
				viewContent : '',
				viewTitle : '',
				permissionCtrl : false,
				radioIndex : '1',
				checkboxIndex : [],				
				voteMsg : {},
				typeCtrl : true,
		        ReqList: {
		          	where: {
		              	offset: 0,
		              	limit: 30,
		              	sortBy: 'createdAt',
		              	order: -1
		            }
		        }				
			}
		},
		methods : {
			viewInform(row){
				this.checkInformCtrl = true
				this.$http.GetInformInfo(row._id)
				.then((data) => {
					if(data.data.content.type === 'vote'){
						this.typeCtrl = false
						this.$http.GetVoteInfo(data.data.entity)
						.then((data) => {
							this.voteMsg = data.data
						})
						return;
					}
					this.typeCtrl = true
					this.viewContent = data.data.content.content
					this.viewTitle = data.data.content.title
				})
			},
			dateFilter(dates = ''){
				if(!dates) return
				let addPrefix = val => {
					val = val < 10 ? '0' + val : val
					return val
				}
			    let a = new Date(dates)
			    let n = a.getFullYear()
			    let y = addPrefix(a.getMonth() + 1)
			    let r = addPrefix(a.getDate())
			    let s = addPrefix(a.getHours())
			    let f = addPrefix(a.getMinutes())
			    let m = addPrefix(a.getSeconds())
			    dates = `${n}-${y}-${r} ${s}:${f}:${m}`
				return dates;
			},
			getInformData(){
	      		this.$http.GetInformList(this.ReqList)
	      		.then((data)=>{
	      			let tempTableData = data.data.list
	      			for(let i = 0; i < tempTableData.length; i++){
	      				tempTableData[i]["type"] = this.filterType(tempTableData[i]["type"])
	      				tempTableData[i]["num"] = (i + 1) + (this.currentPage - 1) * 30
	      				tempTableData[i]["time"] = this.dateFilter(tempTableData[i].createdAt)
	      			}
	      			this.tableData = tempTableData      				
	      			this.counts = data.data.count
	      		})
	      		.catch(error=>{
	      			console.log(error)
	      		})
			},
            backToHome(){
				this.$router.push({
					path: `/${this.$route.params.id}/`
				})            	
            },
            filterType(data){
            	if(data === 'notification'){
            		return this.$t('inform.tip.inform')
            	}else{
            		return this.$t('inform.tip.unknown')
            	}
            },
			//	分页
			currentPageChange(val){
                this.ReqList.where.offset = (val - 1) * 30
                this.getInformData()
			}
		},
		created(){
			if(this.$http.GetCurrentUserInfo().role === 'admin'){
				this.permissionCtrl = true
			}
			this.getInformData()
		}
    }	
</script>


