<template>
    <div class="votes-page">
        <div class="votes-header">
            <div class="votes-header-back" @click="$router.back()" role='button'>
                <i class="fa fa-angle-left fa-2x"></i>
                <span>{{$t('pageCommon.back')}}</span>
			</div>
        </div>
        <div class="votesList">
            <div class="votes-title">
                <h5>表决信息：</h5>
            </div>
            <div class="votesContent" v-for="data in votes" :key="data._id">
                <div class="votesMsg">
                    <h3>{{data.title}}</h3>
                    <div class="voteOption" v-for="option in data.options" :key="option._id">
                        <p>{{option.title}}</p>
                        <p>投票结果: {{option.select.length}}</p>
                    </div>
                </div>
                <div class="myEcharts"></div>
            </div>
        </div>
        
	</div>	
</template>

<script>
    import echarts from 'echarts'
    import './checkVote.scss'
    export default {
        data() {
            return {
                votes:null,
                chart: null,
                option: {
                    title: {
                        text:'',
                        x:'center',
                        textStyle: {
                            fontSize:20
                        }
                    },
                    tooltip: {
                        show:false
                    },
                    // legend: {
                    //     data:['1']
                    // },
                    xAxis: {
                        data: [],
                        axisLabel:{
                            textStyle:{
                                fontSize: 15
                            }
                        }
                    },
                    yAxis: {
                        minInterval: 1,
                        // alignWithLabel: true,
                        axisLabel:{
                            formatter: '{value} %',
                            textStyle:{
                                fontSize:15
                            }
                        }
                    },
                    series: [{
                        name: '统计信息',
                        type: 'bar',
                        data: [],
                        barWidth : 50,
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                formatter: '{c}%',
                                fontSize: 15
                            }
                        }
                    }]
                }
            }
        },
        mounted() {
            
            this.$http.GetVotes(this.$route.query.id)
                .then(res =>{

                    this.votes = res.data
         
                })
        },
        updated() {

            for(var i = 0; i < this.votes.length; i++){
                this.option.xAxis.data = []

                this.option.series[0].data = []

                this.chart = echarts.init(document.getElementsByClassName('myEcharts')[i]);

                this.option.title.text = this.votes[i].title + '数据统计'

                var total = []
        
                for(var j = 0; j < this.votes[i].options.length; j++){
                    var num = 0

                    total = [...total, ...this.votes[i].options[j].select];

                    total = new Set(total)
                
                    total = [...total].length;

                    this.option.xAxis.data.push(this.votes[i].options[j].title)
                    num = parseInt(this.votes[i].options[j].select.length) * 100 / parseInt(total)
                    this.option.series[0].data.push(parseInt(num))
                    // option.series[0].data.push(vote.options[i].select.length)
                }

                this.chart.setOption(this.option);
            }
        }
    }
</script>



