import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'
Vue.use(Vuex)

const state = {
  isShow: false,
  shade:false,
  sliderHandle:false,
  personnelList:false,
  selectBox:false,
  navMap:false,
  jurisdiction:true,
  showPdf:false,
  userName:'管理员',
  allDatas : [],
  officeList: null,
  parentInfo : {},
  treeNode : {
  	treeName : [],
  	totalCount : ''
  },
  toolsInfo: ['加粗', '斜体', '下划线', '删除线', '树线', '标签',
  						'一号字体', '二号字体', '排序', '圆点', '下标', '上标',
  						'光标位置', '对其方式', '清除', '链接', '图片(宽度不能超过540)'
  					]
}
const actions = {
  // 显示弹窗
  getInformation:({commit}, value) => {
    commit(types.GET_INFORMATION, value)
  },
  // 导航菜单滑动
  sliderHandle:({commit}) =>{
    commit(types.SLIDE_NAV)
  },

  // 快速会议导航图
  navShow:({commit}) =>{
    commit(types.NAV_MAP)
  },

  // 显示人员列表
  personnelList:({commit}, value) =>{
    commit(types.PERSONNEL_LIST, value)
  },

  // 显示笔记PDF
  pdfShow:({commit}) => {
    commit(types.SHOW_PDF)
  },

  // 清楚所有弹窗
  allclear:({commit}) =>{
    commit(types.ALL_CLEAR)
  },
  // 权限管理状态
  jurisdiction:({commit}, value) =>{
    commit(types.JURISDICTION, value)
  },
  // 获取树节点
  getParentInfo:({commit}, value) =>{
  	commit(types.PARENTINFO, value)
  },
  // 显示树节点路径
  showPaths:({commit}, value) =>{
  	commit(types.SHOWPATHS, value)
  },
  // 获取所有树节点
  createGroup:({commit}, value) =>{
  	commit(types.CREATE_GROUP, value)
  },
  // 主页总计
  homeTotalCount:({commit}, value) =>{
  	commit(types.HOME_TOTAL_COUNT, value)
  },
  // 保存科室列表
  setOfficeList:({commit}, value) => {
    commit(types.OFFICE_LIST, value)
  }
}

const mutations = {
  [types.OFFICE_LIST](state, value) {
    state.officeList = value
  },
  [types.GET_INFORMATION](state, value) {
    state.isShow = !state.isShow
    state.shade = !state.shade
  },
  [types.SLIDE_NAV](state) {
    state.sliderHandle = !state.sliderHandle
  },
  [types.PERSONNEL_LIST](state, value) {
    state.personnelList = !state.personnelList
    value ? state.selectBox = value : state.selectBox = false
    state.shade = !state.shade
  },
  [types.ALL_CLEAR](state) {
      state.isShow = false
      state.shade = false
      state.personnelList = false
      state.navMap = false
      state.showPdf = false
  },
  [types.JURISDICTION](state, value) {
      value.role === 'admin' ? state.jurisdiction = true : state.jurisdiction = false;
      value.info.name ? state.userName = value.info.name : state.userName = value.name
  },
  [types.PARENTINFO](state, value) {
      state.parentInfo = value
  },
  [types.SHOWPATHS](state, value) {
      state.treeNode.treeName = value
  }, 
  [types.NAV_MAP](state) {
      state.navMap = true
      state.shade = !state.shade
  },
  [types.SHOW_PDF](state) {
      state.showPdf = true
      state.shade = !state.shade
  },
  [types.CREATE_GROUP](state, value) {
      state.allDatas = value
  },
  [types.HOME_TOTAL_COUNT](state, value) {
      state.treeNode.totalCount = value
  }
}

const getters = {
  isShow(state) {
    return state.isShow
  },
  Shade(state) {
    return state.shade
  },
  superior(state) {
     return state.superior
  },
  sliderHandle(state) {
    return state.sliderHandle
  },
  sideData(state){
  	return state.allDatas
  },
  personnelList(state){
  	return state.personnelList
  },
  selectBox(state){
  	return state.selectBox
  },
  jurisdiction(state){
    return state.jurisdiction
  },
  userName(state){
    return state.userName
  },
  homePageCount(state){
  	return state.treeNode.totalCount
  },
  showTreePaths(state){
  	return state.treeNode.treeName
  },  
  getTreeParentInfo(state){
  	return state.parentInfo
  },
  navMap(state){
  	return state.navMap
  },
  showPdf(state){
  	return state.showPdf
  },
  toolsInfoBack(state){
  	return state.toolsInfo
  },
  officeList:state => state.officeList
}
export default new Vuex.Store({
  state,
  actions,
  mutations,
  getters
})



