<template>
    <div class="conference-page">
        <div class="conference-header">
            <div class="conference-header-back" @click="backToHome">
                <i class="fa fa-angle-left fa-2x"></i>
                <span>{{$t('pageCommon.back')}}</span>
            </div>
            <router-link tag='button' to='AddOfficiaConference' class='add-btn' v-if="jurisdiction">{{$t('confPage.create')}}</router-link>
        </div>
        <div class="table-responsive conference-table">
            <table class="table table-striped table-hover">
                <thead>
                    <tr class="row">
                        <th class="col-md-1">{{$t('confPage.prop.num')}}</th>
                        <th class="col-md-2">{{$t('confPage.prop.title')}}</th>
                        <th class="col-md-2">{{$t('confPage.prop.dates')}}</th>
                        <th class="col-md-2">{{$t('confPage.prop.locations')}}</th>
                        <th class="col-md-1">{{$t('confPage.prop.vote')}}</th>
                        <th class="col-md-1">{{$t('confPage.prop.myNote')}}</th>
                        <th class="col-md-1">{{$t('confPage.prop.shareNote')}}</th>
                        <th class="col-md-1">{{$t('confPage.prop.summary')}}</th>
                        <th v-if="jurisdiction">{{$t('confPage.prop.management')}}</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(data, index) in tableData" class="row" :key="index">
                        <td class="col-md-1">{{(index + 1) + pageSize*(currentPage - 1)}}</td>
                        <td class="col-md-2">
                            <span v-if="jurisdiction || data.checkPermit" role="button" @click="checkMeetingMsg(data._id)">{{data.title}}</span>
                            <span v-else>{{data.title}}</span>
                        </td>
                        <td class="col-md-2">{{data.updatedAt | filtersTime}}</td>
                        <td class="col-md-2">{{data.location}}</td>
                        <td class="col-md-1">
                            <el-button v-if="data.vote && jurisdiction || data.vote && data.checkPermit" @click="checkVote(data)">{{$t('confPage.prop.view')}}</el-button>
                            <el-button v-else class='forbid' :disabled="true">{{$t('confPage.prop.view')}}</el-button>
                        </td>
                        <td class="col-md-1">
                            <el-button v-if="jurisdiction || data.checkPermit" @click="checkNote(index, data, 'attend')" class='conference-table-check'>{{$t('confPage.prop.view')}}</el-button>
                            <el-button v-else class='forbid' :disabled="true">{{$t('confPage.prop.view')}}</el-button>
                        </td>
                        <td class="col-md-1">
                            <el-button v-if="jurisdiction && data.sharer.length > 0 || data.sharerPermit" @click="checkNote(index, data, 'sharer')" class='conference-table-check'>{{$t('confPage.prop.view')}}</el-button>
                            <el-button v-else class='forbid' :disabled="true">{{$t('confPage.prop.view')}}</el-button>
                        </td>
                        <td class="col-md-1">
                            <el-button v-if="jurisdiction" class='conference-table-add' @click="skipRecord(index, data)">{{$t('confPage.prop.add')}}</el-button>
                            <el-button v-else-if="data.checkPermit" class='conference-table-add' @click="skipRecord(index, data)">{{$t('confPage.prop.view')}}</el-button>
                            <el-button v-else class='forbid' :disabled="true">{{$t('confPage.prop.view')}}</el-button>
                        </td>
                        <td v-if="jurisdiction" >
                            <el-button class='conference-table-editor' @click="compileConference(data)">{{$t('confPage.prop.edit')}}</el-button>
                            <el-button class='conference-table-delete' @click="delConference(index, data)">{{$t('confPage.prop.delete')}}</el-button>
                            <el-button v-if="data.state && data.state === 'false'" @click="changeState(data)">{{$t('confPage.prop.publish')}}</el-button>
                            <el-button v-else class='forbid' :disabled="true">{{$t('confPage.prop.published')}}</el-button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
		<el-pagination
			mini
			layout="prev, pager, next"
			:current-page.sync="currentPage"
			:page-size='pageSize'
			:total="counts" @current-change="currentPageChange"
			class='conference-pagination' v-if="counts>10">
		</el-pagination>
    </div>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'
    import './conference.scss'
    export default {
        name:'Conference',
        computed: {
            
            ...mapGetters([
                'meetingID',
                'jurisdiction'
            ])
        },
        data() {
            return {
                pageSize : 10,
                currentPage: 1,
                counts: 0,            	
                tableData: [],
                userID:'',
                publisherID:'',
                officialList: {
                    where: {
                        offset: 0,
                        limit: 10,
                        sortBy: 'createdAt',
                        order: -1,
                        group:this.$route.params.id
                    }
                }
            }
        },
        methods: {
            // 封装会议请求
            getConferenceList(){
                this.$http.MeetingList(this.officialList)
                    .then(res =>{
                        
                        for(let item of res.data.list) {
                            
                            for(let personnel of item.personnel){

                                if(this.userID === personnel._id){
                                    item.checkPermit = true
                                    break;
                                }else{
                                    item.checkPermit = false
                                }
                            }

                            for(let sharer of item.sharer) {

                                if(this.userID === sharer._id){
                                    item.sharerPermit = true
                                    break;
                                }else{
                                    item.sharerPermit = false
                                }
                            }
                            for(let topic of item.topics) {
                                
                                if(topic.votes.length){
                                    item.vote = true
                                    break;
                                }else{
                                    item.vote = false
                                }
                            }
                        }
                        this.tableData = res.data.list
                        this.counts = res.data.count
                    })
            },
            filtersTime(value){
                let a = new Date(value.date)
                let n = a.getFullYear()
                let y = a.getMonth() + 1
                let r = a.getDate()
                let s = a.getHours()
                let f = a.getMinutes()
                let m = a.getSeconds()
                s = s < 10 ? '0' + s : s
                f = f < 10 ? '0' + f : f
                m = m < 10 ? '0' + m : m
                let i = `${n}-${y}-${r} ${s}:${f}:${m}`
                return i
            },
			//	分页
			currentPageChange(val){
                this.officialList.where.offset = (val - 1) * 10
                this.getConferenceList()
			},            
            checkMeetingMsg(id){
                this.$router.push({name:'Official', query:{id:id}})
            },
            
            // 编辑正式会议
            compileConference (row) {
                this.$router.push({name:'CompileOfficial', query:{id:row._id}})
            },

            // 删除正式会议
            delConference(index, row){
                this.$confirm(this.$t('messageTip.msgBox.confirm'), this.$t('messageTip.msgBox.tip'), {
                    confirmButtonText: this.$t('messageTip.msgBox.confirmText'),
                    cancelButtonText: this.$t('messageTip.msgBox.cancelText'),
                    type: 'warning'
                }).then(() => {
                    this.$http.delMeeting(row._id)
                        .then(res=> {
                        	let tempoffset = this.officialList.where.offset
                        	let tempcounttwo = (this.counts) / tempoffset
                        	let tempcountone = (this.counts - 1) / tempoffset
                        	if(tempoffset !== 0 && (Math.ceil(tempcounttwo) === this.currentPage) && ((tempcountone) % 1 === 0)){
                        		this.currentPage -= 1
                        	}else{
                        		this.getConferenceList()
                        	}                        	
                        })
                    this.$message({
                    type: 'success',
                    message: this.$t('messageTip.deleteSuccess')
                    });          	
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: this.$t('messageTip.cancelDelete')
                    });          	          
                });
            },
            // 发布更新正式会议
            changeState (data) {
                data.state = 'true'
                this.$http.UpDataMeeting(data._id, data)
                    .then(res => {
                        this.$http.MergePdf(data._id)
                    })
            },
            skipRecord(index, row){
                this.$router.push({name:'AddRecord', query:{id:row._id}})
            },
            checkNote(index, row, val){
                this.$router.push({name:'Note', query:{id:row._id, type:val}})
            },
            checkVote(row){
                this.$router.push({name:'CheckVote', query:{id:row._id}})
            },
            backToHome(){
				this.$router.push({
					path: `/${this.$route.params.id}/`
				})            	
            }
        },
        created(){
            this.userID = this.$http.GetCurrentUserInfo()._id
            this.getConferenceList()
        }
    }   
</script>


