<template>
  <div class="topic">
    <div class="topic-header">
      <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
      <router-link tag="div" to="CollectTopic" v-if="jurisdiction"><el-button>{{$t('topicPage.colTopic')}}</el-button></router-link>
    </div>
    <div class="table-responsive topic-table">
      <table class="table table-striped table-hover">
        <thead>
          <tr class="row">
            <th v-for="key in tableKeys">{{key}}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(data, index) in tableData" class="row">
            <td class="col-md-1">{{index+1}}</td>
            <td class="col-md-3"><span role="button" @click="checkMeetingMsg(data._id)">{{data.title}}</span></td>
            <td class="col-md-4">{{data.updatedAt | filtersTime}}</td>
            <td class="col-md-2">{{data.publisher.name}}</td>
            <td class="col-md-2">{{data.topics.length}}</td>
            <td>
              <el-button @click="saveMeetingId(data._id, 1)">{{$t('topicPage.topic')}}</el-button>
              <el-button v-if="data.status === 4 || data.status === 5 || data.state === 'true'" class="forbid" :disabled="true">{{$t('pageCommon.editorText')}}</el-button>
              <el-button v-else @click="saveMeetingId(data._id, 2)">{{$t('pageCommon.editorText')}}</el-button>
              <el-button @click="delTopic(data)" v-if="jurisdiction">{{$t('pageCommon.deleteText')}}</el-button>
              <el-button v-else class="forbid" :disabled="true">{{$t('pageCommon.deleteText')}}</el-button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <el-pagination mini layout="prev, pager, next" :total="pageCount" @current-change="paging"></el-pagination>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex'
  import './topic.scss'
  export default {
    name: 'topic',
    computed: {
    	tableKeys(){
    		let tableKey = [
	        this.$t('topicPage.prop.num'),
	        '征集主题',
	        this.$t('topicPage.prop.date'),
	        this.$t('topicPage.prop.person'),
	        this.$t('topicPage.prop.total'),
	        this.$t('topicPage.prop.Management')    		
    		]
    		return tableKey
    	},
        ...mapGetters([
            'jurisdiction'
        ])
    },
    data() {
      return {
        tableData: [],
        pageCount:0,
        meetingID:'',
        meetingList: {
          where: {
              offset: 0,
              limit: 10,
              sortBy: 'createdAt',
              order: -1,
              group:this.$route.params.id,
              type:1
            }
          }
        }
    },
    methods: {
      getMeetingList(){
        this.$http.MeetingList(this.meetingList)
          .then(res => {
            this.tableData = res.data.list
            this.pageCount = res.data.count

          })
      },
      delTopic(data) {
        this.$confirm(this.$t('messageTip.msgBox.confirm'), this.$t('messageTip.msgBox.tip'), {
            confirmButtonText: this.$t('messageTip.msgBox.confirmText'),
            cancelButtonText: this.$t('messageTip.msgBox.cancelText'),
            type: 'warning'
          }).then(() => {
            this.$http.delMeeting(data._id)
              .then(res=> {
                  this.getMeetingList()
              })
            this.$message({
              type: 'success',
              message: this.$t('messageTip.deleteSuccess')
            });          	
          }).catch(() => {
          	this.$message({
          	  type: 'info',
          	  message: this.$t('messageTip.cancelDelete')
          	});          	          
          });
      },
      paging(val){
        this.meetingList.where.offset = (val - 1) * 10
        this.getMeetingList()
      },
      saveMeetingId(id, num){
        this.meetingID = {meetingID:id}
        if(num === 2){
          this.$router.push({name:'CollectTopic', query:this.meetingID})
        }else{
          this.$router.push({name:'TopicList', query:this.meetingID})
        }
        
      },
      checkMeetingMsg(id){
        this.meetingID = {meetingID:id}
        this.$router.push({name:'CheckMeeting', query:this.meetingID})
      }
    },
    created() {
      this.getMeetingList()
    }
  }

</script>



