<template>
    <div class="note">
        <div class="note-header">
            <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <!-- <div>
                <el-button>{{$t('note.exports')}}</el-button>
                <el-button>{{$t('note.print')}}</el-button>
            </div> -->
        </div>
        
        <div class="note-main">
            <div class="note-table">
                <table class="table table-striped table-hover">
                    <thead>
                        <tr class="row">
                            <th class="col-md-4">{{$t('note.pop.num')}}</th>
                            <th class="col-md-4">{{$t('note.pop.staff')}}</th>
                            <th class="col-md-4">{{$t('note.pop.state')}}</th>
                        </tr>
                    </thead>
                    <tbody>
                         <tr class="row" v-for="(item, index) in personnels" :class="{'trActive':idx === index}"
                        @click="changeStyle(item, index)">
                            <td class="col-md-4">{{index + 1}}</td>
                            <td class="col-md-4">{{item.name}}</td>
                            <td class="col-md-4" :class="{'tdActive':idx === index}">{{item.status===0?$t('note.unsubmit'):$t('note.submit')}}</td>
                        </tr> 
                    </tbody> 
                </table>
            </div>

            <div class="note-info" v-if="showPdf">
                <div class="note-colse" @click="$store.dispatch('allclear')" role="button">
                    <span>X</span>
                </div>
                <div class="note-message">
                    <div class="pdf-Handle">
                        <!-- <div class="pdf-zoom">
                            <span role="button" @click="pdfZoom(1)"><i class="fa fa-search-plus fa-lg"></i></span>
                            <span>100%</span>
                            <span role="button" @click="pdfZoom(2)"><i class="fa fa-search-minus fa-lg"></i></span>
                        </div> -->

                        <div class="pdf-flip-over">
                            <span role="button" @click="booksPage--, onPrevOrNextPage(booksPage, 'prev')"><i class="fa fa-arrow-left fa-lg"></i></span>
                            <div class="pageNumber">
                                <!-- <input v-model="booksPage" type="text" @change="onPrevOrNextPage(booksPage)"> -->

                                <span>{{booksPage}}</span> <span>/</span> <span>{{numPages}}</span>

                            </div>
                            <span role="button" @click="booksPage++, onPrevOrNextPage(booksPage, 'next')"><i class="fa fa-arrow-right fa-lg"></i></span>
                        </div>

                        <!-- <div class="pdf-bookmark">
                            <span role="button" @click="ss"><i class="fa fa-bookmark fa-lg"></i><i class="fa fa-sort-desc fa-lg"></i></span>
                            <el-tree class="bookMark-list" v-if="dd" :data="bookOutline" :props="defaultProps" :expand-on-click-node="false" highlight-current @node-click="handleNodeClick"></el-tree>
                        </div> -->
                    </div>
                    <!-- <h5>{{$t('note.content')}}：</h5> -->
                    <div class="pdf-file">
                        <canvas id="myCanvas"> </canvas>
                        <canvas id="theCanvas"></canvas>
                    </div>
                </div>
            </div>    
        </div>
    </div>
</template>

<script>
    import {
    mapActions,
    mapGetters
  } from 'vuex'
    import './note.scss'
    export default {
        name:'note',
        data() {
            return {
                idx:'',
                checkAll:false,
                note:[],
                pdfFile:'',
                personnels:Array,
                book:'',
                booksPage:1,
                pageID:'',
                shapes:'',
                group:'',
                showHandle:false,
                numPages:0,
                zoom:1,
                bookOutline:[],
                dd:false,
                defaultProps: {
                    children: 'items',
                    label: 'title'
                }
            }
        },
        computed: {
            ...mapGetters([
                'showPdf'
            ])
        },
        methods:{
            ss(){
                this.dd = !this.dd 
            },
            // 封装弹窗消息
            notification(title, msg, type){
                this.$notify({
                    title: title,
                    message: msg,
                    type: type
                });
            },
            // 封装议题信息
            acquiescent(vel){
                if(vel){
                    this.idx = 0
                }
            },
            // 封装请求议题列表
            topicListHttp(){
                this.$http.GetMeeting(this.$route.query.meetingID)
                .then(res=>{
                    this.MeetingStatus = res.data.status
                    this.topicList = res.data.topics
                    this.topicList.length ? this.acquiescent(this.topicList) : this.acquiescent()
                })
            },

            // 点击选中人员
            changeStyle(item, index){
                this.idx = index;
                this.show = !this.show
                
                if(item.status === 0){
                    this.$notify({
                        title: this.$t('note.warn'),
                        message: this.$t('note.msg'),
                        type: 'warning'
                    });
                    return;
                }else{
                    this.$store.dispatch('pdfShow')
                }

                this.$http.GetPdfFile(this.book, item._id)
                    .then(res =>{
                        var myCanvas = document.getElementById('myCanvas')
                        if(res.data.ReaderFormShapes.length){
                            myCanvas.height = res.data.ReaderFormShapes[0].pageOriginHeight
                            myCanvas.width = res.data.ReaderFormShapes[0].pageOriginWidth
                        }else{
                            myCanvas.height = 841
                            myCanvas.width = 595
                        }

                        this.pdfFile = res.data.doc.location // pdf 文件
                       // this.pageID = res.data.ReaderNoteDocuments[0].readerNotePageNameMap.data // 页ID
                        this.shapes = res.data.ReaderFormShapes // canvasPath
                        paper.setup('myCanvas')
                        
                        this.group = new Group();
                        
                        this.PDFinit(this.booksPage)

                    }).catch(Error =>{
                        if(this.group){
                            this.group.removeChildren();
                        }
                        this.pdfFile = ''
                        this.showHandle = false
                    })
            },
        
            // 初始化pdf
            PDFinit(num) {
                if(!num){
                    num = this.booksPage
                }
                let pdfDoc;
                
                PDFJS.getDocument(this.pdfFile)
                .then((pdf) => {
                    pdfDoc = pdf
                    this.numPages = pdf.numPages
                    return pdf.getPage(Number(num));
                })
                .then((page) => {
                                var scale = this.zoom;
                                var viewport = page.getViewport(scale);
                                var canvas = document.getElementById('theCanvas');
                                var context = canvas.getContext('2d');
                                canvas.height = viewport.height;
                                canvas.width = viewport.width;

                                //
                                // Render PDF page into canvas context
                                //
                                var renderContext = {
                                    canvasContext: context,
                                    viewport: viewport
                                };

                                // 切换页面清空画笔重新渲染
                                if(this.group){
                                    this.group.removeChildren();
                                }
                                let str = (num - 1).toString()
                                for(let i = 0; i < this.shapes.length; i++){
                                    if(this.shapes[i].pageUniqueId === str){
                                        this.pageNum(this.shapes[i])
                                    }
                                }
                   
                                return page.render(renderContext);                                
                })
                .then(res => {
                    return pdfDoc.getOutline();
                })
                .then((res) => {
                    if(!pdfDoc) return;
                    this.showHandle = true
                    this.bookOutline = res
                    // console.log(this.bookOutline)
                })
                .catch(err =>{
                    console.log(err)
                })
            },

            // 封装生成笔画
            pageNum(shap){
                if(shap.formRect){
                    this.UpDateFormData(shap)
                }else{
                    this.UpDateFormLine(shap)
                }
            },
            
            // 画笔路径
            UpDateFormLine(shap){
                let path = new Path();
                let points = shap.points; 
                for (var i = 0; i < points.length; i++) {
                    path.add(new Point(points[i].x, points[i].y));
                }
                
                path.strokeColor = 'black';
                path.strokeWidth = shap.thickness;
                if (shap.review === 1) {
                    path.strokeColor = 'red';
                }
              
                this.group.addChild(path);
                
            },
            
            // formType = 0 单选； 1 多选；
            UpDateFormData(shap){
                let path = new Path();
                let points = new Point(shap.formRect.left, shap.formRect.top);
                let size = new Size(shap.formRect.right - shap.formRect.left + 3, shap.formRect.bottom - shap.formRect.top + 3);
                if(shap.formType === '0'){
                    path = new Path.Ellipse(points, size);
                }else{      
                    path = new Path.Rectangle(points, size);
                }
                path.strokeColor = 'black';
                path.strokeWidth = 1;
                path.fillColor = 'black';
                this.group.addChild(path);
            },
            
            // 上一页或下一页
            onPrevOrNextPage(pages, type) {
                if(pages >= this.numPages){
                    this.booksPage = this.numPages
                    pages = this.numPages
                }else if(pages <= 1){
                    this.booksPage = 1
                     pages = 1
                }

                // if(this.booksPage === 2){
                //     this.booksPage = type === 'next' ? this.booksPage = 3 : this.booksPage = 1
                // }
                this.PDFinit(this.booksPage);
            },
            // pdf缩放
            pdfZoom(key){
                if(key === 1){
                    this.zoom += 0.1
                }else{
                    this.zoom -= 0.1
                }
                this.PDFinit()
            },
            // pdf目录跳转
            handleNodeClick(a, b, c){
                this.booksPage = b.data.$treeNodeId
                this.PDFinit(this.booksPage)
            }
        },
        created(){
            this.$http.GetMeeting(this.$route.query.id)
                .then(res =>{
                    this.book = res.data.book
                    if(this.$route.query.type === 'sharer'){
                        this.$http.getShareNote(this.book)
                        .then(res => {
                            this.personnels = res.data
                        })
                        
                    }else if(this.$route.query.type === 'attend') {
                        this.personnels = res.data.personnel
                    }
                })
        }
    }
</script>


