<template>
  <div class="checkMeeting">
      <div class="checkMeeting-header">
        <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
      </div>
      <div class="checkMeetingTable">
          <table class="checkMeetingTable-table">
            <tr>
              <td>{{$t('checkMeet.title')}}</td>
              <td style="word-break:break-all">
                {{addMeetingTitle}}
              </td>
            </tr>
            <tr>
              <td>{{$t('checkMeet.content')}}</td>
              <td style="word-break:break-all" v-html="addMeetingContent"></td>
            </tr>
            <tr>
              <td>{{$t('checkMeet.receiver')}}</td>
              <td>
                  <span v-for="name in recipients">{{name}}；</span>
              </td>
            </tr>
          </table>
      </div>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex'
  import './checkMeeting.scss'
  export default {
	name: 'checkMeeting',
    data() {
        return {
            checkMeetingChecked: [],
            addMeetingTitle:'',
            addMeetingContent:'',
            recipients:[]
        }
	},
	created(){
		if(!this.$route.query.meetingID) return;

		this.$http.GetMeeting(this.$route.query.meetingID)
        .then(res=>{
            this.addMeetingTitle = res.data.title
            this.addMeetingContent = res.data.content
            res.data.personnel.forEach((item) =>{
                this.recipients.push(item.name)
            })
		    })
	  }
  }

</script>


