import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/system/home/home.vue'
import Topic from '../components/system/topic/topic.vue'
import CollectTopic from '../components/system/collectTopic/collectTopic.vue'
import AddConference from '../components/system/addConference/addConference.vue'
import Inform from '../components/system/inform/inform.vue'
import Vote from '../components/system/vote/vote.vue'
import Conference from '../components/system/conference/conference.vue'
import AddLinkman from '../components/system/addLinkman/addLinkman.vue'
import TopicList from '../components/system/topicList/topicList.vue'
import AddTopic from '../components/system/addTopic/addTopic.vue'
import AddRecord from '../components/system/addRecord/addRecord.vue'
import CheckRecord from '../components/system/checkRecord/checkRecord.vue'
import Device from '../components/system/device/device.vue'
import CheckMeeting from '../components/system/checkMeeting/checkMeeting.vue'
import Note from '../components/system/note/note.vue'
import SendInform from '../components/system/sendInform/sendInform.vue'
import Official from '../components/system/official/official.vue'
import PersonalInfo from '../components/system/personalInfo/personalInfo.vue'
import CompileOfficial from '../components/system/compileOfficial/compileOfficial.vue'
import AddOfficiaConference from '../components/system/addOfficiaConference/addOfficiaConference.vue'
import CheckVote from '../components/system/checkVote/checkVote.vue'

import System from '../components/system/system.vue'
import Login from '../components/login/login.vue'
Vue.use(Router)

export default new Router({
  routes: [{
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'System',
      component: System,
      meta: {
        requireAuth: true
      },      
      children: [{
          path: ':id/',
          name: 'ChildRouter',
          meta: {
            requireAuth: true
          },
          component: Home
        },
        {
          path: ':id/CollectTopic',
          name: 'CollectTopic',
          meta: {
            requireAuth: true
          },
          component: CollectTopic
        },
        {
          path: ':id/AddTopic',
          name: 'AddTopic',
          meta: {
            requireAuth: true
          },
          component: AddTopic
        },
        {
          path: ':id/Topic',
          name: 'Topic',
          meta: {
            requireAuth: true
          },
          component: Topic
        },
        {
          path: ':id/TopicList',
          name: 'TopicList',
          meta: {
            requireAuth: true
          },
          component: TopicList
        },
        {
          path: ':id/AddConference',
          name: 'AddConference',
          meta: {
            requireAuth: true
          },
          component: AddConference
        },
        {
          path: ':id/Inform',
          name: 'Inform',
          meta: {
            requireAuth: true
          },
          component: Inform
        },
        {
          path: ':id/Vote',
          name: 'Vote',
          meta: {
            requireAuth: true
          },
          component: Vote
        },
        {
          path: ':id/Conference',
          name: 'Conference',
          meta: {
            requireAuth: true
          },
          component: Conference
        },
        {
          path: ':id/AddLinkman',
          name: 'AddLinkman',
          meta: {
            requireAuth: true
          },
          component: AddLinkman
        },
        {
          path: ':id/AddRecord',
          name: 'AddRecord',
          meta: {
            requireAuth: true
          },
          component: AddRecord
        },
        {
          path: ':id/CheckRecord',
          name: 'CheckRecord',
          meta: {
            requireAuth: true
          },
          component: CheckRecord
        },
        {
          path: ':id/Device',
          name: 'Device',
          meta: {
            requireAuth: true
          },
          component: Device
        },
        {
          path: ':id/CheckMeeting',
          name: 'CheckMeeting',
          meta: {
            requireAuth: true
          },
          component: CheckMeeting
        },
        {
          path: ':id/Note',
          name: 'Note',
          meta: {
            requireAuth: true
          },
          component: Note
        },
        {
          path: ':id/SendInform',
          name: 'SendInform',
          meta: {
            requireAuth: true
          },
          component: SendInform
        },
        {
          path: ':id/Official',
          name: 'Official',
          meta: {
            requireAuth: true
          },
          component: Official
        },
        {
           path: ':id/CompileOfficial',
           name: 'CompileOfficial',
           meta: {
             requireAuth: true
           },
           component: CompileOfficial
        },
        {
          path: ':id/PersonalInfo',
          name: 'PersonalInfo',
          meta: {
            requireAuth: true
          },
          component: PersonalInfo
       	},
        {
          path: ':id/AddOfficiaConference',
          name: 'AddOfficiaConference',
          meta: {
            requireAuth: true
          },
          component: AddOfficiaConference
        },
        {
          path: ':id/CheckVote',
          name: 'CheckVote',
          meta: {
            requireAuth: true
          },
          component: CheckVote
        }      
      ]
    }
  ]
})



