function dirList(arrs, ids){
	var storeArr = []
	var resultArr = []	
	function fn(arr, id){
		for(let z = 0; z < arr.length; z++){
			if(arr[z]._id === id){
				storeArr.push(arr[z])
				for(let i = 0; i < storeArr.length; i++){
					if(storeArr[i]._id === id){
						var tempId = storeArr[i]._id
						resultArr.push(storeArr[i])
						if(storeArr[i].parent === null){
							return;
						}
						for(let ele = i; ele >= 0; ele--){
							if(storeArr[ele].parent === null){
								resultArr.push(storeArr[ele])
								return;
							}
							for(let y = 0; y < storeArr[ele].children.length; y++){
								if(storeArr[ele].children[y]._id === tempId){
									tempId = storeArr[ele]._id
									resultArr.push(storeArr[ele])
								}
							}
						}
					}
				}
			}
			storeArr.push(arr[z])
			fn(arr[z].children, id)
		}
	}
	
	fn(arrs, ids)
	return resultArr;
}

export default {
	GetDirList(arr, id){
		return dirList(arr, id).reverse();
	}
}


