<template>
    <div class="topicList">
        <div class="topicList-header">
            <p @click="$router.back(-1)"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <div v-if="MeetingState === 'false'">
                
                <el-button @click="UpdateTopicStatus" v-if="meetingConfirmor === userID">{{$t('topicList.confirm')}}</el-button>
                <el-button @click="officialMeeting" v-if="jurisdiction && MeetingStatus === 3">{{$t('topicList.publish')}}</el-button>
                <el-button @click="changeMeetingStatus" v-if="jurisdiction && topicList.length">{{$t('topicList.reported')}}</el-button>
                <el-button @click="$router.push({name:'Official', query:{id:$route.query.meetingID}})" v-if="jurisdiction && topicList.length">{{$t('topicList.preview')}}</el-button>
                <el-button @click="emptyTopicID">{{$t('topicList.add')}}</el-button>
            </div>
        </div>
        
        <div class="topicList-main">
            <div class="topicList-table">
                <table class="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th><el-checkbox  @change="checkedAll" v-model="checkAll" v-if="jurisdiction || meetingConfirmor === userID">{{$t('topicList.allSelcet')}}</el-checkbox></th>
                            <th>{{$t('topicList.allSelcetTd.collect')}}</th>
                            <th>{{$t('topicList.allSelcetTd.state')}}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in topicListComputed" :class="{'trActive':idx === index}"
                        @click="changeStyle(item, index)">
                            <td><el-checkbox @change="selectTopic($event, item, topicList)" v-model="item.check" v-if="jurisdiction || meetingConfirmor === userID"></el-checkbox></td>
                            <td>{{item.title}}</td>
                            <td :class="{'tdActive':idx === index}">{{item.state}}</td>
                        </tr>
                    </tbody> 
                </table>
                <router-link tag="div" class="addTopic-link" :to="{name : 'Topic'}"  role="button">{{$t('topicList.openTopicLib')}}</router-link>
            </div>
            <div class="topicList-info">
                <div class="topicList-content">
                    <p>{{$t('topicList.submitter')}}：{{topicPublisher}}</p>
                    <p>{{$t('topicList.subTime')}}：{{topicTime | filtersTime}}</p>
                    <p v-if="topicStatus !== 3 && topicList.length">
                        <el-button @click="saveTopicID" v-if="jurisdiction || publisherID === userID">{{$t('pageCommon.editorText')}}</el-button>
                        <el-button @click="delTopic" v-if="jurisdiction || publisherID === userID">{{$t('pageCommon.deleteText')}}</el-button>
                    </p>
                </div>
                <div class="topicList-message">
                    <h5>{{$t('topicList.content')}}：</h5>
                    <p v-html="topicContent"></p>

                    <!-- 投票 -->
                    <h3 v-if="VoteInfo.length">投票</h3>
                    <div class="topic-VoteList" v-for="msg in VoteInfo" v-if="VoteInfo.length">             
                        <h4>{{msg.title}}</h4>
                        <label v-for="option in msg.options"> 
                            <input :type="msg.type === 1 ? 'radio' : 'checkbox'" :name="msg._id">
                            {{option.title}}
                        </label>
                    </div>
                    
                    <!-- 附件 -->
                    <h3 v-if="Attachments.length">附件</h3>
                    <div class="topic-Attachments" v-for="file in Attachments" v-if="Attachments.length">
                        <a :href=file.location :download=file.originalname>{{file.originalname}}</a>
                    </div>

                     <!-- <router-link to="/name">{{$t('topicList.editorHis')}}</router-link>  -->
                </div>
            </div>    
        </div>
        <personnel @child="sendStatus" :topics="topics" ref="userData"></personnel>
    </div>
</template>

<script>
    import personnel from '../personnelList/personnelList'
    import {mapActions, mapGetters} from 'vuex'
    import './topicList.scss'
    export default {
        name:'topiclist',
        computed: {
        	topicListComputed(){
        		let tempTopicList = this.topicList
        		if(tempTopicList.length){
        			for(let i = 0; i < tempTopicList.length; i++){
        				if(tempTopicList[i].status === 1){
        					tempTopicList[i].state = this.$t('topicList.popUp.handle')
        				}else if(tempTopicList[i].status === 2){
        					tempTopicList[i].state = this.$t('topicList.popUp.confirm')
        				}else{
        					tempTopicList[i].state = this.$t('topicList.popUp.pass')
        				}
        			}
        		}
        		return tempTopicList
        	},
            ...mapGetters([
                'jurisdiction'
            ])
        },
        data() {
            return {
                idx:'',
                checkAll:false,
                checked:[],
                VoteInfo:[],
                Attachments:[],
                topicList:[],
                topicContent:'',
                topicTime:'',
                topicID:'',
                topicPublisher:'',
                MeetingStatus:0,
                MeetingState:null,
                topicStatus:1,
                topics:[],
                meetingConfirmor:'',
                userID:'',
                publisherID:''
            }
        },
        methods:{
            // 封装弹窗消息
            notification(title, msg, type){
                this.$notify({
                    title: title,
                    message: msg,
                    type: type
                });
            },
            // 封装议题信息
            acquiescent(val){
                if(val){
                    this.topicContent = val[0].content
                    this.topicTime = val[0].updatedAt
                    this.topicID = val[0]._id
                    this.topicPublisher = val[0].publisher.name
                    this.VoteInfo = val[0].votes
                    this.Attachments = val[0].attachments
                    this.topicStatus = val[0].status
                    this.idx = 0
                }else{
                    this.topicContent = ''
                    this.topicTime = ''
                    this.topicID = ''
                    this.topicPublisher = ''
                    this.VoteInfo = []
                    this.Attachments = []
                    this.topicStatus = 1
                }  
            },
            // 封装请求议题列表
            topicListHttp(){
                this.$http.GetMeeting(this.$route.query.meetingID)
                .then(res=>{
                    this.checkAll = false
                     res.data.topics.forEach((item)=>{
                            item.check = false
                            this.publisherID = item.publisher._id
                    })
                    this.topicList = res.data.topics
                    this.MeetingStatus = res.data.status
                    this.MeetingState = res.data.state
                    this.topicList.length ? this.acquiescent(this.topicList) : this.acquiescent()
                    if(!res.data.confirmor) return;
                    this.meetingConfirmor 
                    for(let confirmorID of res.data.confirmor){
                        if(this.userID === confirmorID._id) {
                            this.meetingConfirmor = confirmorID._id
                            break;
                        }
                    }

                })
            },
            // 上报会议 判断是否上报过||是正式会议
            changeMeetingStatus(){
                if(this.MeetingStatus === 3){

                    this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipFive'), 'warning')

                }else if(this.MeetingStatus === 4){
                    this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipSix'), 'warning')

                }else if(!this.topicList.length || !this.topics.length){

                    this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipSeven'), 'warning')

                }else{
                    this.$refs.userData.GetPersonnelList()
                    this.$store.dispatch('personnelList')
                }
                
            },
            // 点击选中当前议题
            changeStyle(item, index){
                item.votes.length ? this.VoteInfo = item.votes : this.VoteInfo = [];
                item.attachments.length ? this.Attachments = item.attachments : this.Attachments = [];
                this.topicContent = item.content
                this.topicTime = item.updatedAt
                this.topicID = item._id
                this.topicPublisher = item.publisher.name
                this.topicStatus = item.status
                this.idx = index;
            },
            checkedAll(){  
                if(!this.checkAll){
                    for(let i = 0; i < this.topicList.length; i++){
                        this.topicList[i].check = false;
                    }
                    return;
                }
                for(let i = 0; i < this.topicList.length; i++){
                        this.topicList[i].check = true;
                        this.topics.push(this.topicList[i]._id)
                }
            },
            // 创建议题
            emptyTopicID() {
                
                this.$router.push({name:'AddTopic', query:this.$route.query})
            },
            // 删除议题
            delTopic(){
               if(!this.topicID || !this.$route.query.meetingID || !this.topicList.length) return;
                this.$http.DelTopic(this.$route.query.meetingID, {topic:this.topicID})
                    .then(res =>{
                        this.topicListHttp()
                        this.topicList.length ? this.acquiescent(this.topicList) : this.acquiescent()
                    })
            },
            // 编辑议题
            saveTopicID(){
                this.$router.push({name:'AddTopic', query:{topicID:this.topicID, meetingID:this.$route.query.meetingID}})
            },
            // 选择议题添加到数组
            selectTopic(event, item, data){
                if(event.target.checked){
                    this.topics.push(item._id)
                    this.topicID = item._id
                    this.topics.length === data.length ? this.checkAll = true : this.checkAll = false
                }else{
                    for(let i = 0; i < this.topics.length; i++){
                        if(this.topics[i] === this.topicID){
                            this.topics.splice(i, 1)
                        }
                    }
                    this.topics.length === data.length ? this.checkAll = true : this.checkAll = false
                }
            },
            // 更新议题状态
            UpdateTopicStatus(){
                if(!this.topics.length){
                    this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipOne'), 'warning')
                    return;
                }else if(this.topicStatus === 3){
                    this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipTwo'), 'warning')
                }else{
                    let decision = {
                        topics:this.topics
                    }
                    this.$http.UpdateTopicStatus(this.$route.query.meetingID, decision)
                        .then(res =>{
                            this.notification('提示', '确认成功', 'info')
                            this.topicListHttp()
                        })
                }
                
            },
            // 发布正式会议信息补全,判断是否上报||是正式会议
            officialMeeting(){
                 if(this.MeetingStatus === 3){
                    this.$router.push({name:'AddConference', query:{meetingID:this.$route.query.meetingID}})
                 }else if(this.MeetingStatus === 4){

                     this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipThree'), 'warning')

                 }else{
                     this.notification(this.$t('topicList.popUp.warning'), this.$t('topicList.popUp.tipFour'), 'warning')
                 }
                 
            },
            // 接收会议状态值
            sendStatus(val){
                this.MeetingStatus = val
                this.topicListHttp()
            }
        },
        components: {
            personnel
        },
        created(){
            this.userID = this.$http.GetCurrentUserInfo()._id
            if(!this.$route.query.meetingID) return;
            this.topicListHttp()
        }
    }
</script>


