<template>
    <div class="addConference-page">
        <div class="addConference-header">
            <div class="addConference-header-back" @click="$router.back()" role='button'>
                <i class="fa fa-angle-left fa-2x"></i>
                <span>{{$t('pageCommon.back')}}</span>
            </div>
            <button class="add-btn" @click="createOfficialMeeting">{{$t('addConf.add')}}</button>
        </div>
        <div class="addConference-body">
        	<form action="" method="get">
				<table class="addConference-table">
					<tr>
						<td>{{$t('addConf.confName')}}</td>
						<td><input type="text" class="addConference-table-input" v-model="officialMeeting.title"/></td>
					</tr>
			   		<tr class="second-line">
			   			<td colspan="2"></td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addConf.date')}}</td>
			   			<td>
							<div class="block">
								<el-date-picker
								v-model="officialMeeting.date"
								type="datetime"
								:placeholder="$t('addConf.dataTd.selectDate')">
								</el-date-picker>
							</div>
						</td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addConf.site')}}</td>
			   			<td><input type="text" class="addConference-table-input" v-model="officialMeeting.location"/></td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addConf.speaker')}}</td>
						   <!-- <td><input type="text" class="addConference-table-input" v-model="officialMeeting.host"/></td> -->

						   <!-- 临时 -->
						   <td>
								<select  v-model="officialMeeting.host" class="addConference-table-input">
									<option value=""></option>
									<option :value=items.name v-for="items in hosthostList">{{items.name}}</option>
								</select>
						   </td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addConf.receiver')}}</td>
			   			<td>
							<el-tag :key="name" type="primary" v-for="(name, index) in recipients" :closable="true" :close-transition="false" @close="delUser(name, index)">
								{{name}}
							</el-tag>
							<span role='button'  @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="addConference-table-add">{{$t('addConf.receiverTd.add')}}</span></span>
			   			</td>
			   		</tr>
			   		<!-- <tr>
			   			<td>{{$t('addConf.require')}}</td>
			   			<td>
							<el-checkbox>{{$t('addConf.requireTd.receive')}}</el-checkbox>
							<el-checkbox>{{$t('addConf.requireTd.reply')}}</el-checkbox>	   				
			   			</td>
			   		</tr>
			   		<tr>
			   			<td>{{$t('addConf.level')}}</td>
			   			<td>
							<el-checkbox>{{$t('addConf.levelTd.secrecy')}}</el-checkbox>	   				
			   			</td>
			   		</tr>		   		 -->
				</table>
			</form>	
		</div>
		<personnel @userInfo = "userInfo" ref="userData"></personnel>
    </div>    
</template>

<script>
	import VueFileUpload from 'vue-file-upload';
	import {mapActions, mapGetters} from 'vuex'
	import personnel from '../personnelList/personnelList'
	import './addConference.scss'
    export default {
		name:'AddConference',
		data() {
			return {
				recipients:[],
				showVote:false,
                atuoBol : false,
				bol : false,
				addTopicChecked : '',
				hosthostList:[],
				contrast: '',
				officialMeeting:{
					title:'',
					date:'',
					location:'',
					host:'',
					personnel:[],
					state: 'true',
					status:4
				}
			}
		},
		methods : {
			createOfficialMeeting(){
				if(this.contrast !== this.officialMeeting.title){
					this.officialMeeting.status = 3
					this.officialMeeting.isUpdateTitle = 'true'
				}
				this.$http.AddOfficialMeeting(this.$route.query.meetingID, this.officialMeeting)
					.then(res => {
						return this.$http.MergePdf(res.data._id)
					})
					.then(res =>{
						this.$router.push({name:'Conference'})
					})
			},

			// ==================================议题投票处理======================================
			addVote() {
                let vote = {
                        title:'',
                        type:'',
                        options:[]
                };
                this.addTopicInfo.votes.push(vote);
			},
			
            addOption(vote) {
                let option = {
                        title:''
                    };
                    if (!vote.options) {
                        vote.options = []
                    }
        
                vote.options.push(option);
			},
			
            // 删除投票
            removeVote(data, index){
                data === 1 ? this.addTopicInfo.votes.splice(index, 1) : data.splice(index, 1)
			},
			
			// =================人员信息=========================
			userInfo(user){
                if(!this.recipients || !this.officialMeeting.personnel){
                    user.forEach((item) =>{
                        this.recipients.push(item.name)
                        this.officialMeeting.personnel.push(item._id)       
                    })
                    return;
                }
                let names = [];
                let nameID = [];
                user.forEach((item) =>{
                    names.push(item.name)
                    nameID.push(item._id)         
                })
                for(let i = 0; i < names.length; i++){
                     this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0; 
                     this.officialMeeting.personnel.indexOf(nameID[i]) === -1 ? this.officialMeeting.personnel.push(nameID[i]) : 0;
                }
			},
			delUser(name, index) {
				this.recipients.splice(index, 1)
				this.officialMeeting.personnel.splice(index, 1)
				this.$refs.userData.deleteUser(index)
			}
		},
		computed: {
            showType(){
                 this.addTopicInfo.type === '研究类议题' ? this.showVote = true : this.showVote = false
                 return this.showVote
            }
        },
		components: {
			personnel,
			VueFileUpload
		},
		created() {
			// 人员信息====》主讲人
			this.$http.GetPersonnelList()
                .then(res =>{
                    this.hosthostList = res.data
				})			

			if(!this.$route.query.meetingID) return;
			this.$http.GetMeeting(this.$route.query.meetingID)
				.then(res => {
					this.officialMeeting.personnel = res.data.personnel.map((item) =>{
						return item._id;
					})

					this.officialMeeting.title = res.data.title
					this.contrast = res.data.title
					res.data.personnel.forEach(item =>{
						this.recipients.push(item.name)
					})
				})
		}
    }	
</script>


