<template>
        <div class="compileOfficial">
          <div class="compileOfficial-header">
            <p @click="$router.push({name:'Conference'})"><i class="fa fa-angle-left fa-2x"></i><span>{{$t('pageCommon.back')}}</span></p>
            <div class="compileOfficial-handle" v-if="statusNum === 4 || statusNum === 5">
                <el-button @click="compileTopic(2)">{{$t('official.addTopic')}}</el-button>
                <el-button @click="compileTopic(1)">{{$t('official.save')}}</el-button>
            </div>
          </div>
          <div class="compileOfficial-allMsg">
            <!-- 会议 -->
              <div class="compileOfficialTable">
                  <table class="compileOfficialTable-table">
                    <tr>
                      <td>{{$t('official.table.title')}}</td>
                      <td style="word-break:break-all">
                        <input type="text" v-model="officialInfo.title">
                      </td>
                    </tr>
                    <tr>
                      <td>{{$t('official.table.date')}}</td>
                      <td>
                          <el-date-picker
                          v-model="officialInfo.date"
                          type="datetime"
                          :placeholder="$t('addConf.dataTd.selectDate')">
                          </el-date-picker>
                      </td>
                    </tr>
                    <tr>
                      <td>{{$t('official.table.site')}}</td>
                      <td>
                          <input type="text" v-model="officialInfo.location">
                      </td>
                    </tr>
                    <!-- <tr>
                      <td>{{$t('official.table.content')}}</td>
                      <td style="word-break:break-all"> <quill-editor v-model="officialInfo.content" :options="editorOption"></quill-editor></td>
                    </tr> -->
                    <tr>
                      <td>{{$t('official.table.speaker')}}</td>
                      <!-- <td>
                          <input type="text" v-model="officialInfo.host">
                      </td> -->

                      <!-- 临时 -->
                      <td>
                        <select  v-model="officialInfo.host" class="addConference-table-input">
                          <option value=""></option>
                          <option :value=items.name v-for="items in hostList">{{items.name}}</option>
                        </select>
                      </td>

                    </tr>
                    <tr>
                      <td>{{$t('official.table.receiver')}}</td>
                      <td v-if="statusNum === 4 || statusNum === 5">
                        <el-tag :key="name" type="primary" v-for="(name, index) in recipients" :closable="true" :close-transition="false" @close="delUser(name, index)">
                          {{name}}
                        </el-tag>
                        <span role='button' @click="$refs.userData.GetPersonnelList(), $store.dispatch('personnelList')"><i class="fa fa-plus fa-lg"></i><span class="compileOfficialTable-table-add">{{$t('official.table.receiverTd.selectStaff')}}</span></span>
                      </td>
                      <td v-else>
                          <span :key="name" v-for="(name, index) in recipients">
                              {{name}}；
                          </span>
                      </td>
                    </tr>
                  </table>
                </div>

                <!-- 议题 -->
                 <div class="compileOfficial-Topic" v-for="(topic, index) in officialInfo.topics" v-if="officialInfo.topics.length">
                  <div class="compileOfficial-content">
                      <p>{{$t('official.submitter')}}：{{topic.publisher.name}}</p>     
                      <p>{{$t('official.type')}}：{{topic.type}}</p>
                      <p>{{$t('official.department')}}：{{topic.department}}</p>
                      <p>{{$t('official.supervisor')}}：{{topic.supervisor}}</p>
                      <p>{{$t('official.subTime')}}：{{topic.updatedAt | filtersTime}}</p>
                      <p>
                        <el-button @click.sopp="compileTopic(topic)">{{$t('pageCommon.editorText')}}</el-button>
                        <el-button @click.sopp="deleteTopic(topic, index, officialInfo.topics)">{{$t('pageCommon.deleteText')}}</el-button>
                      </p>
                  </div>
                  <div class="compileOfficial-message">
                    <h3>{{topic.title}}</h3>
                    <h5>{{$t('official.content')}}：</h5>
                    <p v-html="topic.content"></p>
                    <h4 v-if="topic.votes.length">{{$t('official.vote')}}</h4>
                    <div class="compileOfficial-Vote" v-for="msg in topic.votes" v-if="topic.votes.length">
                      <h4>{{msg.title}}</h4>
                      <label v-for="option in msg.options"> 
                          <input :type="msg.type === 1 ? 'radio' : 'checkbox'" :name="msg._id">
                          {{option.title}}
                      </label>
                    </div>
                    <h4 v-if="topic.attachments.length">{{$t('official.adjunct')}}</h4>
                    <div class="compileOfficial-Attachments" v-for="file in topic.attachments" v-if="topic.attachments">
                        <a :href=file.path download>{{file.originalname}}</a>
                    </div>
                  </div>
                </div> 
          </div>
          <personnelList @userInfo="userInfo" ref="userData"></personnelList>
        </div>
      </template>
      
      <script>
        import Quill from 'quill';
        import { ImageResize } from '../packageJS/ImageResize.js';
        Quill.register('modules/imageResize', ImageResize);
        import personnelList from '../personnelList/personnelList.vue'
        import './compileOfficial.scss'
        export default {
          name: 'compileOfficial',
          data() {
            return {
              recipients: [],
              recipientsID: [],
              statusNum: '',
              mark:false,
              n:0,
              hostList:[],
              editorOption: {
                  modules: { 
                      imageResize: {
                          displaySize: true
                      }
                  }
              },
              officialInfo: {
                confirmor: Object,
                title: '',
                content: '',
                date: '',
                location: '',
                host: '',
                personnel: Array,
                piblisher: Object,
                topics: Array
              }
            }
          },
          watch: {
            officialInfo:{
              handler:function() {
                
                this.n++
                if(this.n === 1 || this.n === 2) return;
                this.mark = true
              },
              deep: true
            },
            recipientsID: function() {
              this.n++
              if(this.n === 1 || this.n === 2) return;
              this.mark = true
            }
          },
          methods: {
            getMeetings () {
              if (!this.$route.query.id) return;
              this.$http.GetMeeting(this.$route.query.id)
                .then(res => {
                    this.statusNum = res.data.status
                    this.officialInfo = res.data
                    this.compareInfo = this.officialInfo
                    res.data.personnel.forEach((item) => {
                      this.recipients.push(item.name)
                      this.recipientsID.push(item._id)
                    })
                    for(let item of this.officialInfo.topics){
                        if(item.publisher === null){
                            item.publisher = ''
                        }
                    }
                })
            },

            // 人员组件传参
            userInfo (user) {
              if (!this.recipients || !this.recipientsID) {
                user.forEach((item) => {
                  this.recipients.push(item.name)
                  this.recipientsID.push(item._id)
                })
                return;
              }
              let names = [];
              let nameID = [];
              user.forEach((item) => {
                names.push(item.name)
                nameID.push(item._id)
              })
              for (let i = 0; i < names.length; i++) {
                this.recipients.indexOf(names[i]) === -1 ? this.recipients.push(names[i]) : 0;
                this.recipientsID.indexOf(nameID[i]) === -1 ? this.recipientsID.push(nameID[i]) : 0;
              }
            },

            // 删除收件人
            delUser (name, index) {
              this.recipients.splice(index, 1)
              this.recipientsID.splice(index, 1)
              this.$refs.userData.deleteUser(index)
            },

            // 增加或编辑议题
            compileTopic (val) {
                if(this.mark) {
                    let data = {
                      title : this.officialInfo.title,
                      date : this.officialInfo.date,
                      location : this.officialInfo.location,
                      content : this.officialInfo.content,
                      host : this.officialInfo.host,
                      status : 4,
                      personnel : this.recipientsID
                    }
                    this.$http.UpDataMeeting(this.$route.query.id, data)
                      .then(res => {
                          this.mark = false
                          
                          if(val === 1){
                            this.$http.MergePdf(this.$route.query.id)
                              .then(res=> { 
                                this.$router.push({name:'Conference'})
                            })
                          }else if (val === 2){
                            this.$router.push({name:'AddTopic', query:{meetingID:this.$route.query.id}})
                          }else{
                            this.$router.push({name:'AddTopic', query:{topicID:val._id, meetingID:this.$route.query.id}})
                          }

                      })
                    return;
                }
      
                  if(val === 1){
                    this.$http.MergePdf(this.$route.query.id)
                      .then(res=> { 
                        this.$router.push({name:'Conference'})
                      })
                    
                  }else if (val === 2){
                    this.$router.push({name:'AddTopic', query:{meetingID:this.$route.query.id}})
                  }else{
                    this.$router.push({name:'AddTopic', query:{topicID:val._id, meetingID:this.$route.query.id}})
                  }
                
            },

            // 删除议题
            deleteTopic (topic, index, topics) {
              this.$http.DelTopic(this.$route.query.id, {topic:topic._id})
                    .then(res =>{
                        topics.splice(index, 1)
                        this.$http.MergePdf(this.$route.query.id)
                    })
            }
          },
          components: {
            personnelList
          },
          mounted() {
              this.getMeetings()

              // 人员信息====》主讲人
              this.$http.GetPersonnelList()
                .then(res =>{
                    this.hostList = res.data
                })
          }
        }
      
      </script>
      


