<template>
  <el-row type="flex" class="app-header" justify="space-between" align='middle'>
    <el-col :span="12" class="app-header-left">
      <i class="fa fa-bars" role="button" @click="sliderHandle"></i>
			<!-- <span>{{$t("systemName")}}</span> -->
			<span>{{$t("systemTitle")}}</span>
    </el-col>
    <el-col :span="12" class="app-header-right">
			<!-- <el-dropdown class='app-header-dropDown' @command='switchLang' @visible-change='languageDropdown=!languageDropdown'>
					<span class="el-dropdown-link" role='button'>{{$t("language")}}
						<i :class="[languageDropdown ? 'el-icon-caret-bottom' : 'el-icon-caret-top', 'el-icon--right']"></i>
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item command='Zh'>中文</el-dropdown-item>
						<el-dropdown-item command='En'>English</el-dropdown-item>
					</el-dropdown-menu>
			</el-dropdown> -->
      <span><a href="javascript:;"><img src="" alt=""></a></span>
      <el-dropdown class="admin-menu"  @command='personalInfo' @visible-change='userDropdown=!userDropdown'>
        <span class="el-dropdown-link">
					{{userName}}<i :class="[userDropdown ? 'el-icon-caret-bottom' : 'el-icon-caret-top', 'el-icon--right']"></i>
				</span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command='a'>{{$t("personalInformation")}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div role='button' @click="exitSystem" class="exit-system">{{$t("exit")}}</div>
    </el-col>
  </el-row>
</template>

<script>
import DirList from '../packageJS/dirList.js'
import {mapActions, mapGetters} from 'vuex'
import './header.scss'
export default {
	data(){
	    return {
	    	languageDropdown : true,
	    	userDropdown : true
	    }
  	},
  	computed: {
    	...mapGetters([
      		'userName',
  			'sideData'
    	])
	},
	methods: {
	    ...mapActions([
	      	'sliderHandle'
		]),
		personalInfo(command){
			if(command === 'a'){
				this.$router.push({name:'PersonalInfo'})
			}
		},
		exitSystem(){
				window.localStorage.removeItem('token')
				window.localStorage.removeItem('userInfo')
				window.localStorage.removeItem('language')
				this.$router.push({name:'Login'})
				this.$store.dispatch('showPaths', [])
		},
		switchLang(command){
			// 语言切换
			if(command === 'Zh'){
		    	this.$i18n.locale = 'zh-CN'
		      localStorage.setItem('language', 'zh-CN')
			}else if(command === 'En'){
				this.$i18n.locale = 'en-US'
				localStorage.setItem('language', 'en-US')
			}
			this.language()
		},
		language(){
			// 路由显示预处理并发送
			let routerTemp = this.$route.path.split('/')
			let allDataTemp = DirList.GetDirList(this.sideData, this.$route.params.id)
			if(routerTemp[routerTemp.length - 1]) {
				for(let key in this.$t('allRouter')[0]){
					if(routerTemp[routerTemp.length - 1] === key){
						allDataTemp.push({
							name : this.$t('allRouter')[0][key]
						})
						break;
					}
				}
			}
			this.$store.dispatch('showPaths', allDataTemp)    	
	    }
	}
}
</script>


